import control from "_platform/dsf/mixins/control";

/**
 * 段落包装
 * @param label       段落标题
 * @param itemId       段落id
 * @param margin      段落间隙
 * @param rows        段落内容
 * @param index       段落索引
 * @returns {string}
 */
function sectionPack(label, itemId, margin, rows, index) {
  if (!rows || !rows.length) {
    return '';
  }
  if (label === '基本信息') {
    itemId = 'jiBenXinXi';
  }
  return `<dsf-one-thing-section
    ref="section-${index}"
    :margin-top="${margin.top ? 'true' : 'false'}"
    :margin-bottom="${margin.bottom ? 'true' : 'false'}"
    label="${label}"
    item-id="${itemId}">
    ${rows.join('\n')}
  </dsf-one-thing-section>`;
}

function findParentSubTable(parents) {
  for (let i = parents.length - 1; i >= 0; i--) {
    if (parents[i].isOneToMany) {
      return parents[i];
    }
  }
  return null;
}

function createTemplateBefore(node, parents, dynamicForm) {
  // console.log(node.ctrlType);
  let ctrlProps = dsf.fillComponentDefaultProps(node);
  dsf.mix(node, ctrlProps);
  if (!node.designId) {
    node.designId = dsf.uuid();
  }
  node.isDesign = false;

  if (node.isOneToMany) {
    node.metadata.level = 1;
    node.metadata.id = dynamicForm.$prefix + "_" + node.caption;
    node.metadata_fullcode = dynamicForm.$prefix + "_" + node.caption;
  } else if (node.isFormControl) {
    let subTable = findParentSubTable(parents);
    if (subTable) {
      //在子表内
      node.metadata.level = 1;
      node.metadata.id = subTable.metadata_fullcode + "." + node.caption;
      node.metadata_fullcode = subTable.metadata_fullcode + "." + node.caption;
    } else {
      node.metadata.level = 0;
      node.metadata.id = dynamicForm.$prefix + "." + node.caption;
      node.metadata_fullcode = dynamicForm.$prefix + "." + node.caption;
    }
  }
  if (dsf.isDef(dynamicForm.childrenDev[node.caption])) {
    // if (dsf.isUnDef(compProps.development['attrs'])) {
    //   compProps.development['attrs'] = {};
    // }
    node.development['attrs'] = dynamicForm.childrenDev[node.caption] || {};
  }
}

/**
 * 拼出模板
 * @param parent
 * @returns {string}
 */
function templateFormat(parent) {
  let updateCtrlList = [], // 存放上传控件
    normalCtrlList = [], // 存放其他控件
    normalCtrlList2 = {},
    hideCtrlList = []; // 存放其他控件
  let result = null,
    props = [],
    dicts = {},
    compsMap = {};
  const childrenDev = parent.childrenDev;
  parent.$data.$components.forEach((control, index1) => {
    let section = [];
    let extract = {}; // 提取包含@的表单项
    control.controls.forEach((comp, index2) => {
      let compProps = comp.props;
      // 如果是上传控件
      let filesMetaDataKey = ['file-meta-data', 'photo-meta-data'];
      if (filesMetaDataKey.indexOf(compProps.metadata.ckey) >= 0 && comp.label) {
        compProps.tags = comp.label;
      }
      // 分割“@”
      let extractLabel = compProps.label.split('@');
      if (extractLabel.length > 1) {
        compProps.label = extractLabel[0];
        if (!extract[extractLabel[1]]) {
          extract[extractLabel[1]] = [];
        }
      }
      if (compProps.isFormControl && parent.$vm.queryString.isview == "1") {
        compProps.readOnly = true;
      }
      if (!compProps.designId) {
        compProps.designId = dsf.uuid();
      }
      let dict = comp.dict;
      if (dsf.isDef(childrenDev[compProps.caption])) {
        // if (dsf.isUnDef(compProps.development['attrs'])) {
        //   compProps.development['attrs'] = {};
        // }
        compProps.development['attrs'] = childrenDev[compProps.caption] || {};
      }
      // 为所有下拉框设置选项dom渲染时间为“首次获得焦点时渲染”
      if (compProps.ctrlType === 'dsf.select') {
        compProps.optionRenderTime = 1;
      }
      if (compProps.caption) {
        parent.componentsMap[compProps.caption] = compProps;
      }
      if (compProps.isFormControl && !compProps.isOneToMany) {
        //组件初始化状态被隐藏但设置的必填则组件忽略隐藏设置强制显示
        let oldAttr = compProps.development['attrs'] ? (compProps.development['attrs'][":visible"] || "") : "";
        let visableArr = [];
        visableArr.push(`formControlHideOrShow('${compProps.designId}')`);
        if (oldAttr) {
          visableArr.push(`(${oldAttr})`);
        }
        compProps.development['attrs'][":visible"] = visableArr.join("&&");
      }
      result = dsf.designer.convertTemplateAndControlData(compProps, false, {
        before: function(node, parents) {
          //转换为模板前对属性数据的处理
          createTemplateBefore(node, parents, parent);
        }
      });

      let colVisible = ` :visible="${compProps.development['attrs'][":visible"]}"`;
      // let colVisible = ` :visible="formControlHideOrShow('${compProps.designId}')"`;
      _.each(result.props, (el, i) => {
        // 记录表单项初始隐藏信息
        el.defaultVisable = el.visible;
        // let objKeys = Object.keys(result.props);
        // if (!el.visible && objKeys && objKeys.length) {
        //   colVisible = ` :visible="$controls['${objKeys[0]}'].visible"`;
        // }
        parent.$vm.$set(parent.$vm.$controls, i, el);
      })

      let tpl = result.tpl;
      if (!comp.isHide) {
        tpl = `<dsf-col${colVisible} :span="components[${index1}].controls[${index2}].props.colSpan">${tpl}</dsf-col>`;
      }
      if (dict) {
        if (compProps.isOneToMany) {
          _.each(dict, (el, i) => {
            let key = (parent.$prefix + '_' + compProps.metadata.code) + "." + i;
            dicts[key] = el;
          });
        } else {
          let key = parent.$prefix + '.' + compProps.metadata.code;
          dicts[key] = dict;
        }
      }
      if (comp.isHide) {
        // 如果是隐藏控件
        hideCtrlList.push(tpl);
      } else if (filesMetaDataKey.indexOf(compProps.metadata.ckey) >= 0) {
        // 如果是上传控件
        updateCtrlList.push(tpl);
      } else if (extractLabel.length > 1) {
        // 如果包含“@”
        extract[extractLabel[1]].push(tpl);
      } else {
        // 如果是其他控件
        section.push(tpl);
      }
    });
    for (let key in extract) {
      if (!extract[key].length) {
        break;
      }
      section.push(`<dsf-col>
        <div style="width: 100%">
          <dsf-title4 style="background-color: rgba(0,0,0,.03)" value="${key}" showColorLump showWeight></dsf-title4>
          <dsf-row>
            ${extract[key].join('\n')}
          </dsf-row>
        </div>
      </dsf-col>`);
    }
    normalCtrlList2[control.rmId] = {
      name: control.pageName,
      itemId: control.rmId,
      controls: section
    };
  });
  let matterNames = parent.$vm.matterNames || [];
  if (parent.$vm.matterItemIds) {
    normalCtrlList = parent.$vm.matterItemIds.map((itemId, i) => {
      let ctrl = normalCtrlList2[itemId];
      if (ctrl && ctrl.controls.length) {
        return normalCtrlList2[itemId];
      }
      return {
        name: matterNames[i],
        itemId,
        controls: ['']
      }
    });
  }
  normalCtrlList2[''] && normalCtrlList.unshift(normalCtrlList2['']);
  normalCtrlList2 = undefined;
  matterNames = undefined;
  result = undefined;
  parent = null;
  return {
    tpl: '<div>\n' +
      normalCtrlList.map(({ name, itemId, controls }, index) => {
        return sectionPack(name, itemId, { top: false, bottom: false }, controls, index + 1)
      }).join('') +
      sectionPack('附件信息', '000', { top: false, bottom: false }, updateCtrlList, 0) +
      '<div style="display: none;">\n\r' + hideCtrlList.join('\n\r') + '\n</div>' +
      '\n</div>',
    props: props,
    dicts: dicts,
    compsMap: compsMap,
    hasAnnexes: !!updateCtrlList.length // 是否有附件
  }
}
// var sizeof = require('object-sizeof');
// 动态生成模板
export default function(parent) {
  let result = templateFormat(parent);
  let renderFn = Vue.compile(result.tpl);
  // console.log(sizeof(result)) 
  result.tpl = null;
  // console.log(render);
  return {
    name: 'DynamicFormTemplate',
    render: renderFn.render,
    ctrlType: "dynamicBlock",
    mixins: [control],
    data() {
      return {
        components: parent.$data.$components,
        dicts: result.dicts,
        lastData: parent.lastData
      }
    },
    computed: {
      $viewData() {
        return this.$vm.$viewData
      },
      $controls() {
        return this.$vm.$controls
      },
      $viewInitData() {
        return this.$vm.$viewInitData;
      },
      $enum() {
        return this.$vm.$enum;
      },
    },
    beforeCreate() {

    },
    created() {

    },
    // beforeMount(){

    // },
    beforeMount() {
      // console.time();
      let defaultValues = {};
      _.each(this.dicts, (d, i) => {
        if (dsf.isDef(d.dataSource)) {
          this.$set(this.$enum, i, d.dataSource);
        }
        if (dsf.isDef(d.defaultValue)) {
          defaultValues[i] = d.defaultValue;
        }
      });
      //将近期数据或默认值传递给组件
      let allValue = dsf.mix(defaultValues, this.lastData);

      _.each(allValue, (v, i) => {
        // let baseInfokey = parent.baseinfoKey;
        // let code = i.split(".")[1];
        // if (code) {
        //   let bk = dsf.type(parent.baseinfoKey) == 'array' ? parent.baseinfoKey : []
        //   //将动态表单的值更新表单基本信息
        //   _.find(bk, (k) => {
        //     let baseCode = k.split(".")[1];
        //     if (baseCode && baseCode == code) {
        //       this.$set(this.$viewData, k, v);
        //     }
        //   });
        // }
        this.$set(this.$viewInitData, i,{defaultValue:v});
      });
      for (let k in this.$viewInitData) {
        //子表key没有.
        //主表数据key前缀为parent.$prefix + "."；
        if (k.startsWith(parent.$prefix + ".") || k.indexOf(".") < 0) {
          if (!dsf.hasOwn(this.$viewData, k) || this.$viewData[k] === undefined) {
            if(this.$viewInitData[k].defaultValue){
              let v=this.$viewInitData[k].defaultValue;
              this.$set(this.$viewData, k,v);
            }
          }
        }
      }
      this.$nextTick(() => {
        const dom = document.getElementsByClassName('ds-one-thing-form-select');
        if (dom && dom.length && dom[0].__vue__) {
          const $formSelect = dom[0].__vue__;
          result.hasAnnexes && $formSelect.matter.push({
            text: '附件信息',
            itemId: '000',
            value: '附件信息'
          });
          $formSelect.onScroll();
        }
        this.$forceUpdate();

        this.$emit("loaded");
      });
    },
    beforeDestroy() {
      result.tpl = null;
      result.props = null;
      result.dicts = null;
      result.normalCtrlList = null;
      result = null;
      parent.lastData = null;
      parent.$components = [];
      parent.components = [];
      parent.componentsMap = {};
      parent = null;
      console.log('销毁动态表单')
    },
    methods: {
      formControlHideOrShow(key) {
        // console.log("d")
        // if (this.$refs[key]) {
        //   let ref = this.$refs[key];
        //   if (!ref.value && ref.required) {
        //     return true;
        //   }
        //   if (!ref.props.visible) {
        //     return false;
        //   } else {
        //     return true;
        //   }
        // } else {
        //   return false;
        // }
        //获取初始化组件的属性
        let componetProps = this.$controls[key];
        //获取组件的值
        // let componetValue = this.$viewData[componetProps.metadata_fullcode];
        //如果值为空时但是设置了必填项则必须显示
        // if ((dsf.isUnDef(componetValue) || componetValue == '') && componetProps.required) {
        //   return true;
        // }
        return componetProps.visible;
      },
      hiddenOrShow(key) {
        let component = this.$refs[key];
        if (component) {
          return component.$props.visible;
        } else {
          return false;
        }
      },
      //子表或者列表在循环输出单元格时对属性进行复制并传递给updateControlPropsInTemplate函数
      updateControlPropsInTemplate(designId, scope) {
        return this.$vm.updateControlPropsInTemplate(designId, scope)
      }
    }
  }
}