<template>
  <div class="send-flow">
    <div class="userbox">
      <template v-if="list && list.length > 0">
        <span v-for="(item, index) in list" :key="index">{{ item }}</span>
      </template>
      <template v-if="msg">
        <span>{{ msg }}</span>
      </template>
    </div>
    <div class="count-down">
      <span>页面将在</span>
      <span class="ds-button__icon-text">{{ time }}</span>
      <span>秒钟后自动关闭</span>
    </div>
  </div>
</template>
<script>
import { MessageBox } from "element-ui";
export default {
  name: "SendFlowDialog",
  props: {
    list: {
      type: Array,
      default() {
        return [];
      },
    },
    msg: {
      type: String,
      default() {
        return "";
      },
    },
  },
  data() {
    return {
      time: 3,
    };
  },
  mounted() {
    setTimeout(() => {
      this.countDown();
    }, 1000);
  },
  methods: {
    yes() {
      return true;
    },
    countDown() {
      this.time--;
      if (this.time === 0) {
        document.querySelector(".sendFlow").click();
        if (window.opener) {
          window.close();
        }
      } else {
        setTimeout(() => {
          this.countDown();
        }, 1000);
      }
    },
  },
};
</script>
<style lang="scss" scoped>
.send-flow {
  .userbox {
    text-align: center;
    color: rgb(64, 158, 255);
    margin-top: 20px;
    span {
      display: inline-block;
      margin-right: 10px;
    }
  }
  .count-down {
    text-align: center;
    margin-top: 20px;
    color: rgb(170, 170, 170);
    .ds-button__icon-text {
      min-width: 20px;
    }
  }
}
</style>
