<template>
  <div v-loading="loading" class="news-detail">
    <h1 class="title">{{ detail.title }}</h1>
    <div class="meta">
      <span class="name">{{ detail.author }}</span>
      <em>{{ detail.releaseTime | dateTime('yyyy-mm-dd') }}</em>
    </div>
    <div v-if="detail.content" class="content">
      <p>简介：{{ detail.introduction }}</p>正文内容：
      <div v-html="detail.content"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "NewsDetail",
  data() {
    return {
      loading: false,
      detail: "",
      detailId: this.$route.params.id || ''
    };
  },
  created() {
    this.getDetail();
  },
  methods: {
    getDetail() {
      this.loading = true;
      let params = {
        pageName: "t_001",
        namespace: "ot.t_01",
        data: JSON.stringify({
          _id: this.detailId,
        }),
      };
      let url = "/meta/data?id=" + this.detailId;
      this.$http
        .get(url, params)
        .then(({ data }) => {
          let content = data.data;
          this.detail = {
            title: content["ot_t_01.y_tzgg_001"],
            author: content['ot_t_01.ds_create_user_name'],
            releaseTime: content["ot_t_01.ds_create_time"],
            introduction: content["ot_t_01.y_tzgg_002"],
            content: content["ot_t_01.y_tzgg_005"],
          };
          this.loading = false;
        })
        .catch((ex) => {
          dsf.layer.message("加载异常", false);
        })
        .finally(() => {
          this.loading = false;
        });
    },
  },
  beforeRouteEnter(to, from, next) {
    // 添加背景色
    document.querySelector('body').setAttribute('style', 'background-color: #fff')
    next()
  },
  beforeRouteLeave(to, from, next) {
    // 去除背景色
    document.querySelector('body').setAttribute('style', '')
    next()
  }
};
</script>