<template>
  <DsfOneThingBox
    title="消息中心"
    link="/pc/ot/dolib/list"
    :is-go="dataSource.length > 0"
  >
    <div v-if="dataSource.length" slot="title" class="title-tag">
      <span class="item" @click="goTo">待提交<span class="num"></span></span>
    </div>
    <div slot="content" v-loading="loading" class="dsf-onething-message">
      <div
        v-for="(item, $index) in dataSource"
        :key="$index"
        class="message-item"
      >
        <div class="message-title">
          <a
            :href="item.url"
            target="_blank"
            :title="`您为 ${item.target_name} 办理的${item.item_name}审批${item.apply_state}，同批次事项已完成${item.jd}。`"
          >
            您为 {{ item.target_name }} 办理的{{ item.item_name }}审批{{
              item.apply_state
            }}，同批次事项已完成{{ item.jd }}。
          </a>
        </div>
        <div class="message-date">
          {{ item.updated_time | dateTime("yyyy-mm-dd hh:ii") }}
        </div>
      </div>
      <p v-if="!loading && !dataSource.length" class="dsf-onething-no-message">
        暂无数据
      </p>
    </div>
  </DsfOneThingBox>
</template>
<script>
import control from "_platform/dsf/mixins/control";
import DsfOneThingBox from "./DsfOneThingBox";
export default dsf.component({
  ctrlCaption: "消息中心",
  mixins: [control],
  name: "DsfOneThingMessage",
  components: {
    DsfOneThingBox,
  },
  data() {
    return {
      loading: "",
      dataSource: [],
    };
  },
  created() {
    this.getMessageData();
  },
  methods: {
    getMessageData() {
      this.loading = true;
      this.$http
        .get("ot/notice/getMessageData")
        .then(({ data }) => {
          let list = _.map(data.data, (el) => {
            return {
              ...el,
              url:
                "/page.html#/pc/" +
                el.namespace.replace(/\./g, "/") +
                "/" +
                el.pagename +
                "?id=" +
                el.id +
                "&applyno=" +
                el.apply_no +
                "&isview=1",
            };
          });
          // 截取数组长度为5条
          this.dataSource = _.slice(list, 0, 5);
          this.loading = false;
        })
        .catch(() => {
          dsf.layer.message("加载异常", false);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    goTo() {
      this.$router.push({ path: "/pc/ot/fillin/list?tj=暂存" });
    },
  },
});
</script>
