<template>
  <div class="ds-control ds-one-thing-scene-select" :class="getCss">
    <div class="header">
      <div class="header-breadcrumb">
        <span class="header-breadcrumb-item" @click="switchScene(true)">
          {{ isSceneCopy ? '场景选择' : '场景' }}
        </span>
        <span v-if="sceneActive" v-show="!isSceneCopy && sceneActive.name" class="header-breadcrumb-item">
          {{ sceneActive.name }}
        </span>
      </div>
      <div v-show="!isSceneCopy && sceneActive" class="header-more" @click="switchScene(true)">切换场景</div>
    </div>
    <div v-show="!loaded" class="no-data">
      数据加载中...
    </div>
    <div v-if="loaded" class="body">
      <transition name="scene-select-slide-fade">
        <div v-show="isSceneCopy" class="body-box scene-box">
          <div v-for="scene in sceneList" :key="scene['ot_cm_id']" class="body-type">
            <div class="body-type-label">{{ scene.name }}：</div>
            <div v-if="scene.childItem" class="body-type-list">
              <div
                v-for="item in scene.childItem"
                :key="item['ot_cm_id']"
                :class="{active: sceneActive && sceneActive['ot_cm_id'] === item['ot_cm_id']}"
                class="scene-type-item"
                @click="sceneClick(item)"
              >
                {{ item.name }}
              </div>
            </div>
          </div>
        </div>
      </transition>
      <transition name="scene-select-slide-fade">
        <div v-show="!isSceneCopy" class="body-box child-box">
          <div v-for="scene in matterList" :key="scene['ot_cm_id']" class="body-type">
            <div class="body-type-label">{{ scene.name }}：</div>
            <div v-if="scene.childItem" class="body-type-list">
              <el-checkbox-group v-if="radioMap[scene['ot_cm_id']] == undefined" v-model="checkList">
                <template v-for="({ds_order, ot_cm_id, itemid, name, code, task_handle_item}) in scene.childItem">
                  <el-checkbox
                    :key="ot_cm_id"
                    :label="(ds_order || 0) +
                      '[-]' + ot_cm_id +
                      '[-]' + (itemid || '') +
                      '[-]' + name +
                      '[-]' + (code || '') +
                      '[-]' + (task_handle_item || '')"
                    :disabled="isDisabled(ot_cm_id)"
                  >
                    <el-tooltip v-if="disableAll || thingsNoJbr.indexOf(ot_cm_id) > -1" class="item" effect="light" :content="disableAll?'查询维护经办人信息异常':'请维护经办人信息'" placement="top">
                      <span style="color: #F56C6C;">{{ name }}</span>
                    </el-tooltip>
                    <span v-else-if="!tipsList[ot_cm_id]">{{ name }}</span>
                    <el-tooltip v-else class="item" effect="light" :content="tipsList[ot_cm_id]" placement="top">
                      <span style="color: #F56C6C;">{{ name }}</span>
                    </el-tooltip>
                  </el-checkbox>
                </template>
              </el-checkbox-group>
              <el-radio-group v-else v-model="radioMap[scene['ot_cm_id']]">
                <template v-for="({ds_order, ot_cm_id, itemid, name, code, task_handle_item}) in scene.childItem">
                  <el-radio
                    :key="ot_cm_id"
                    :label="(ds_order || 0) +
                      '[-]' + ot_cm_id +
                      '[-]' + (itemid || '') +
                      '[-]' + name +
                      '[-]' + (code || '') +
                      '[-]' + (task_handle_item || '')"
                    :disabled="isDisabled(ot_cm_id)"
                    @click.native.stop="radioClick(scene['ot_cm_id'], ot_cm_id)"
                  >
                    <el-tooltip v-if="disableAll || thingsNoJbr.indexOf(ot_cm_id) > -1" class="item" effect="light" :content="disableAll?'查询维护经办人信息异常':'请维护经办人信息'" placement="top">
                      <span style="color: #F56C6C;">{{ name }}</span>
                    </el-tooltip>
                    <span v-else-if="!tipsList[ot_cm_id]">{{ name }}</span>
                    <el-tooltip v-else class="item" effect="light" :content="tipsList[ot_cm_id]" placement="top">
                      <span style="color: #F56C6C;">{{ name }}</span>
                    </el-tooltip>
                  </el-radio>
                </template>
              </el-radio-group>
            </div>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import control from "_platform/dsf/mixins/control";

export default dsf.component({
  ctrlCaption: "场景选择",
  name: 'DsfOneThingSceneSelect',
  mixins: [control],
  props: {
    isScene: {
      type: Boolean,
      default: true,
      desc: "true 场景选择窗口，false 事项选择窗口"
    },
    metaData: {
      type: Object,
      required: false
    },
    $vmProps: {
      type: Object,
      required: false
    }
  },
  data() {
    return {
      loaded: false,
      isSceneCopy: true,
      sceneActive: null,
      // 是否禁用所有
      disableAll: false,
      keysAction: {},
      singleConfig: {},
      tipsList: {},
      checkList: [],
      radioMap: {},
      sceneList: [],
      // 人员对应的非维护事项，需要禁用且不能被选中
      thingsNoJbr: []
    }
  },
  computed: {
    vp () {
      return this.$vmProps || this.$vm || {};
    },
    matterList () {
      return this.sceneActive && this.sceneActive.childItem || [];
    },
    // 是否隐藏模式，
    hiddenModel () {
      return this.vp.hiddenModel || false;
    },
    // 哪些行是需要单选的
    radioIdList () {
      let vp = this.vp;
      if (vp && vp.sceneRadioIdList) {
        return vp.sceneRadioIdList;
      }
      return [];
    },
    // 哪些项是需要隐藏的
    // 隐藏模式下表示不需要禁用的
    hideIdList () {
      let vp = this.vp;
      if (vp && vp.sceneHideIdList && vp.sceneHideIdList.length) {
        let checkList = [];
        let tipsList = {};
        let list = vp.sceneHideIdList.map(v => {
          v = v.split('-');
          if (v.length > 1) {
            checkList.push(v.length > 1 ? v[1] == 1 : undefined)
          }
          if (v.length > 2) {
            tipsList[v[0]] = v[2];
          }
          return v[0];
        });
        this.tipsList = tipsList;
        this.toggel(list, checkList);
        return list;
      }
      return [];
    }
  },
  watch: {
    'sceneActive' (to) {
      this.switchScene(false);
      if (to && !this.keysAction[to.ot_cm_id]) {
        let keys = {};
        to.childItem.forEach(v => {
          v.childItem.forEach(({ds_order, ot_cm_id, itemid, name, code, task_handle_item}) => {
            keys[ot_cm_id] = [
              ds_order || 0,
              ot_cm_id,
              itemid || '',
              name,
              code || '',
              task_handle_item || ''
            ].join('[-]');
          });
        })
        this.keysAction[to.ot_cm_id] = keys;
      }
    },
    'radioIdList' (to) {
      to.forEach(v => {
        this.radioMap[v] = '';
        if (!this.radioMap[v] || this.radioMap[v] == '') {
          this.$set(this.radioMap, v, '');
        }
      })
    }
  },
  created () {
    const radioMap = {};
    this.radioIdList.forEach(v => {
      radioMap[v] = '';
    })
    this.radioMap = radioMap;
    this.isSceneCopy = this.isScene;
    if (!this.isDesign) {
      this.postData();
    }
  },
  methods: {
    // 是否禁用选项
    isDisabled(id) {
      if (this.disableAll || this.thingsNoJbr.indexOf(id) > -1) {
        return true;
      }
      let has = this.hideIdList.indexOf(id) > -1;
      return this.hiddenModel ? !has : has;
    },
    // 选中或取消
    toggel(ids, checkedList) {
      let keysAction = this.keysAction[this.sceneActive.ot_cm_id];
      if (!keysAction) return;
      ids.forEach((id, i) => {
        if (checkedList[i] == undefined) return;
        let pid = this.singleConfig[id];
        let key = keysAction[id];
        // 单选
        if (this.radioMap[pid]) {
          if (this.radioMap[pid] == key && !checkedList[i]) {
            this.radioMap[pid] = '';
          } else if (this.radioMap[pid] != key && checkedList[i]) {
            this.radioMap[pid] = key;
          }
        }
        // 多选
        else {
          let index = this.checkList.indexOf(key);
          if (index > -1 && !checkedList[i]) {
            this.checkList.splice(index, 1);
          } else if (checkedList[i]) {
            this.checkList.push(key);
          }
        }
      })
    },
    // 请求数据
    postData() {
      this.loaded = false;
      this.$http.get('ot/item/queryItemByItemId', {
        itemId: '48090b2502164d1ab6a046d51677c06d'
      }).done(({ success, data }) => {
        if (success) {
          this.postDataSuccess(data.childItem || []);
        }
      }).always(() => {
        this.loaded = true;
      })
    },
    postDataSuccess (data) {
      this.sceneList = data;
      if (this.metaData) {
        let { typeid, text, value, itemids, order, code, taskHandleItem } = this.metaData;
        if (typeid) {
          for (let i = 0; i < data.length; i++) {
            for (let j = 0; j < data[i].childItem.length; j++) {
              const item = data[i].childItem[j];
              if (typeid === item['ot_cm_id']) {
                this.sceneActive = item;
                this.vp.sceneId = item['ot_cm_id'];
                break;
              }
            }
            if (this.sceneActive) break;
          }
        }
        if (this.sceneActive) {
          this.singleConfig = {};
          _.forEach(this.sceneActive.childItem, v1 => {
            _.forEach(v1.childItem, v2 => {
              this.singleConfig[v2['ot_cm_id']] = v2['treeinfo_pid'];
            })
          })
          if (text && value && itemids && order) {
            value = value.split(",");
            text = text.split(",");
            itemids = itemids.split(",");
            order = order.split(",");
            code = code.split(",");
            taskHandleItem = taskHandleItem.split(",");
            let radioMap = Object.assign({}, this.radioMap);
            order.forEach((v, i) => {
              let treeinfoPid = this.singleConfig[value[i]];
              let item = [
                v,
                value[i],
                itemids[i],
                text[i],
                code[i],
                taskHandleItem[i]
              ].join('[-]');
              if (this.radioMap[treeinfoPid] != undefined) {
                radioMap[treeinfoPid] = item;
              } else {
                this.checkList.push(item);
              }
            });
            this.radioMap = radioMap;
          }
          this.isSceneCopy = false;
          this.$emit('update:isScene', false);
        }
      }
    },
    /**
     * 切换场景
     * @param isScene   状态
     * @param isClear   是否清空多选
     */
    switchScene(isScene, isClear = true) {
      if (isScene !== this.isSceneCopy) {
        this.isSceneCopy = isScene;
        this.$emit('update:isScene', isScene);
        if (!isScene && isClear) {
          this.checkList = [];
          const radioMap = {};
          this.radioIdList.forEach(v => {
            radioMap[v] = '';
          })
          this.radioMap = radioMap;
        }
      }
    },
    // 场景选择
    sceneClick(item) {
      if (!this.sceneActive || this.sceneActive['ot_cm_id'] !== item['ot_cm_id']) {
        this.sceneActive = item;
        this.vp.sceneId = item['ot_cm_id'];
        this.switchScene(false);
      } else {
        this.switchScene(false, false);
      }
    },
    // 单选项点击
    radioClick(pid, id) {
      if (this.isDisabled(id)) {
        return;
      }
      let v = this.radioMap[pid];
      if (v && v.indexOf(id) > -1) {
        setTimeout(() => {
          v = this.radioMap[pid];
          if (v != '') {
            this.$set(this.radioMap, pid, '');
          }
          v = null;
        }, 100)
      }
    },
    /**************二次开发相关请求***************/
    // type, typeid, text, itemids, value, order, code, taskHandleItem
    yes () {
      let list = _.concat([], this.checkList || [], _.filter(this.radioMap, v => v != ''));
      if (!list || !list.length) {
        return null;
      }
      const type = this.sceneActive['name'];
      const typeid = this.sceneActive['ot_cm_id'];
      let arr = list.map(v => {
        let nv = v.split('[-]');
        return {
          taskHandleItem: nv[5] === "null" ? "" : nv[5],
          code: nv[4] === "null" ? "" : nv[4],
          text: nv[3] === "null" ? "" : nv[3],
          itemId: nv[2] === "null" ? "" : nv[2],
          value: nv[1] === "null" ? "" : nv[1],
          order: parseInt(nv[0] === "null" ? "-1" : nv[0])
        };
      }).sort(function (a, b) {
        return a.order - b.order;
      });
      return {
        type,
        typeid,
        text: arr.map(v => v.text).join(','),
        itemids: arr.map(v => v.itemId).join(','),
        value: arr.map(v => v.value).join(','),
        order: arr.map(v => v.order).join(','),
        code: arr.map(v => v.code).join(','),
        taskHandleItem: arr.map(v => v.taskHandleItem).join(',')
      }
    },
    checkScene(_this, args, loading) {
      let loading2 = null;
      if (!loading) {
        loading2 = dsf.layer.loading();
      }
      return new Promise((resolve, reject) => {
        let res = {};
        this.getThingsNoJbr(args, loading2 || loading)
          .then(() => {
            this.sbkfQuery(_this, args, loading2 || loading).then(userSbInfo => {
              res = Object.assign(res, userSbInfo);
            }).catch(err => {
              reject(err);
            }).always(() => {
              this.gjjQuery(_this, args, loading2 || loading).then(userGjjInfo => {
                let message = {};
                if (res.message) {
                  message = {message: res.message};
                }
                res = Object.assign(res, userGjjInfo, message);
              }).catch(err => {
                reject(err);
              }).always(() => {
                resolve(res);
                loading2 && loading2.close();
              });
            });
          })
          .catch(err => {
            reject(err);
          });
      });
    },
    // 社保开发查询
    sbkfQuery(_this, args, loading) {
      let loading2 = null;
      if (loading) {
        loading.text = '社保信息查询...'
      } else {
        loading2 = dsf.layer.loading({
          text: '社保信息查询...'
        });
      }
      return new Promise((resolve, reject) => {
        this.$http.get('/accfund/getSocial', {
          sxlx: _this.sxlx, // 事项类型
          zjhm: args.idcard, // 证件编号
          xm: args.name, // 姓名
          tyshxym: args.tyshxym || '', // 统一社会信用码
          zzjgdm: args.zzjgdm || '', // 组织机构代码
          shbxm: args.shbxm || '', // 社会保险码
          txny: args.txny || '' // 退休年月（事项类型为退休时必传）
        })
          .done(res => {
            if (res.success) {
              this.sbkfQuerySuccess(_this, _this.sxlx, res.data, resolve);
            } else {
              this.hiddenScene1(_this, 0, '社保信息查询异常');
              this.hiddenScene2(_this, 0, '社保信息查询异常');
              this.hiddenScene3(_this, 0, '社保信息查询异常');
              reject(Error('社保信息查询异常'));
            }
          })
          .error(() => {
            this.hiddenScene1(_this, 0, '社保信息查询异常');
            this.hiddenScene2(_this, 0, '社保信息查询异常');
            this.hiddenScene3(_this, 0, '社保信息查询异常');
            reject(Error('社保信息查询异常'));
          })
          .always(() => {
            loading2 && loading2.close();
          });
      });
    },
    sbkfQuerySuccess(_this, sxlx, data, resolve) {
      const sbzhzt = data['sbzhzt']; // 社保账户状态
      let cjgzny = data['cjgzny']; // 参加工作年月
      let sfws = data['sfws']; // 是否完丧
      let sjrzzw = data['sjrzzw']; // 职务职级
      let jb = data['jb']; // 级别薪级
      let gznx = data['gznx']; // 全部缴费年限
      let jfjs14 = data['jfjs14']; // 2014年9月本人月工资收入纳入个人缴费基数金额
      let message = data['message'];
      if (message && message.length > 0) {
        this.hiddenScene1(_this, 0, message);
        this.hiddenScene2(_this, 0, message);
        this.hiddenScene3(_this, 0, message);
        resolve({message});
        return;
      }
      if (sbzhzt == "null") {
        this.hiddenScene2(_this, 0, message);
        this.hiddenScene3(_this, 0, message);
        resolve({});
        return;
      }

      let type = 0;
      // 页面上只能让该人员做参保缴费新增, 账户转出和退休是隐藏的
      if (dsf.isUnDef(sbzhzt) || dsf.isUnDef(cjgzny) || dsf.isUnDef(sfws)) {
        if (sxlx != 3) {
          this.hiddenScene2(_this);
          this.hiddenScene3(_this);
        }
        type = 1;
      }
      // 把场景中的账户转出和退休事项隐藏
      else if (sbzhzt == 31 || sbzhzt == 32) {
        if (sxlx != 3) {
          this.hiddenScene2(_this);
          this.hiddenScene3(_this);
        }
        type = 2;
      }
      // 可以做社会保险-账户转出与社会保险-退休事项，不能做新设、转入，所以社会保险-参保缴费事项隐藏。
      else {
        if (sxlx != 3) {
          this.hiddenScene1(_this);
        }
        type = 3;
      }
      if (dsf.isNumber(cjgzny) && cjgzny.length == 6) {
        cjgzny = cjgzny.substring(0,4) + '-' + cjgzny.substring(4,6) + '-01';
      } else {
        cjgzny = '';
      }
      if (dsf.isDef(sfws)) {
        sfws = sfws == 0 ? {text: '否', value: '0'} : {text: '是', value: '1'};
      } else {
        sfws = null;
      }
      resolve({
        userSbInfo: {
          sbzhzt,
          cjgzny,
          sfws,
          sjrzzw,
          jb,
          gznx,
          jfjs14,
          type
        }
      })
    },
    // 公积金信息查询
    gjjQuery(_this, args, loading) {
      let loading2 = null;
      if (loading) {
        loading.text = '公积金信息查询...'
      } else {
        loading2 = dsf.layer.loading({
          text: '公积金信息查询...'
        });
      }
      _this.userGjj = {};
      return new Promise((resolve, reject) => {
        this.$http.get('/accfund/getAccfund', {
          zjhm: args.idcard,
          creditCode: args.usccid
        })
          .done(res => {
            if (res.success) {
              this.gjjQuerySuccess(_this, res.data, resolve);
            } else {
              this.hiddenScene4(_this, 0, '公积金信息查询异常。');
              this.hiddenScene5(_this, 0, '公积金信息查询异常。');
              this.hiddenScene6(_this, 0, '公积金信息查询异常。');
              reject(Error('公积金信息查询异常'));
            }
          })
          .error(() => {
            this.hiddenScene4(_this, 0, '公积金信息查询异常。');
            this.hiddenScene5(_this, 0, '公积金信息查询异常。');
            this.hiddenScene6(_this, 0, '公积金信息查询异常。');
            reject(Error('公积金信息查询异常'));
          })
          .always(() => {
            loading2 && loading2.close();
          });
      });
    },
    gjjQuerySuccess(_this, data, resolve) {
      const GRZH = data['GRZH']; // 个人基本账户
      const BGRZH = data['BGRZH']; // 个人补充账户
      const DWZH = data['DWZH']; // 基本单位账号1
      const BDWZH = data['BDWZH']; // 单位补充账户1
      const UNIT_CODE = data['UNIT_CODE']; // 基本单位账号2
      const X_UNIT_CODE = data['X_UNIT_CODE']; // 单位补充账户2
      let message = data['message'];
      if (message && message.length > 0) {
        this.hiddenScene5(_this, 0, message);
        this.hiddenScene6(_this, 0, message);
        resolve({message});
        return;
      }

      if (UNIT_CODE && X_UNIT_CODE) {
        if (GRZH && BGRZH && DWZH && BDWZH) {
          // 1、场景：无需办理公积金开设和转入，可办理停缴退休
          if (DWZH == UNIT_CODE) {
            this.hiddenScene4(_this);
            this.hiddenScene5(_this);
          }
          // 2、场景：上一个单位的公积金未办理转出，需要协调给与提示。
          else if (DWZH != UNIT_CODE) {
            this.hiddenScene4(_this, 0, '该职工尚未从上家单位转出，请联系上家单位进行转出。');
            this.hiddenScene5(_this, 0, '该职工尚未从上家单位转出，请联系上家单位进行转出。');
          }
        }
        // 3、场景：可以办理公积金开设，隐藏转入和停缴退休。
        else if (!GRZH && !BGRZH && !DWZH && !BDWZH) {
          this.hiddenScene5(_this);
          this.hiddenScene6(_this);
        }
        // 4、场景：可以办理公积金转入，隐藏开设和停缴
        else if (GRZH && BGRZH && !DWZH && !BDWZH) {
          this.hiddenScene4(_this);
          this.hiddenScene6(_this);
        }
        // 5.场景：可以同时办理公积金开设和转入，隐藏退休
        else if (GRZH && !BGRZH && !DWZH && !BDWZH) {
          this.hiddenScene6(_this);
        }
      } else {
        this.hiddenScene4(_this, 0, '未查询到单位公积金信息。');
        this.hiddenScene5(_this, 0, '未查询到单位公积金信息。');
        this.hiddenScene6(_this, 0, '未查询到单位公积金信息。');
      }

      // 存公积金信息
      resolve({
        userGjjInfo: {
          // 个人基本账号
          y_gjj_055: GRZH,
          // 个人补充账号
          y_gjj_056: BGRZH,
          // 单位基本账号
          y_gjj_053: UNIT_CODE,
          // 单位补充账号
          y_gjj_0531: X_UNIT_CODE
        }
      });
    },
    // 匹配身份证是否在数据库中
    checkIdCard(_this, idcard, loading) {
      let loading2 = null;
      if (loading) {
        loading.text = '正在匹配人员信息...'
      } else {
        loading2 = dsf.layer.loading({
          text: '正在匹配人员信息...'
        });
      }
      return new Promise((resolve, reject) => {
        this.$http.get('/dbsource/interface/ot/otherpage/gwylxidcard', {
          q: idcard
        })
          .done(function (res) {
            if (res.success) {
              const data = res.data;
              if (!data || !data.length) {
                reject(Error('该人员不在本单位'));
              } else if (data.length > 1) {
                reject(Error('匹配到重复人员信息'));
              } else if (data[0].idcard != idcard) {
                reject(Error('请输入完整的身份证信息'));
              } else {
                resolve();
              }
            } else {
              reject(Error('该人员不在本公司'));
            }
          })
          .error(() => {
            reject(Error('匹配人员信息异常'));
          })
          .always(function () {
            loading2 && loading2.close();
          });
      });
    },
