      let loading2 = null;
      if (loading) {
        loading.text = '正在查询维护事项...'
      } else {
        loading2 = dsf.layer.loading({
          text: '正在查询维护事项...'
        });
      }
      this.disableAll = false;
      return new Promise((resolve, reject) => {
        this.$http.get('/company/getThingsNoJbr', {
          id: args.idcard
        }).done(({success, data}) => {
          if (success) {
            this.getThingsNoJbrSuccess(data.ot_cm_id);
            resolve();
          } else {
            this.disableAll = true;
            reject(Error('查询维护经办人信息异常'));
          }
        }).error(() => {
          this.disableAll = true;
          reject(Error('查询维护经办人信息异常'));
        }).always(function () {
          loading2 && loading2.close();
        });
      });
