<template>
  <div class="ds-control ds-no-padding ds-one-thing-service-box">
    <DsfOneThingBox :is-go="false">
      <template slot="title">
        <div class="service-tag">
          <div v-for="(item, index) in services" :key="item.id" class="line">
            <span :class="{ active: current === index }" @click="handleTag(index, item)">{{ item.name }}</span>
          </div>
        </div>
      </template>
      <template slot="content">
        <div class="ds-one-thing-service-item-box" :class="{'big-font': current == 1}">
          <div v-for="(item, index) in items[current]" :key="index" class="ds-one-thing-service-item" :title="item.name">
            <div class="service-item-icon" @click="handelEvent(item)"><img :src="[ item.ele ? item.icon : require('../../public/img/service/'+item.icon) ]"></div>
            <div class="service-item-info">{{ item.name }}</div>
          </div>
        </div>
      </template>
    </DsfOneThingBox>
  </div>
</template>
<script>
import control from "_platform/dsf/mixins/control";
import DsfOneThingBox from "./DsfOneThingBox"

export default dsf.component({
  ctrlCaption: "服务中心组件",
  mixins: [control],
  name: "DsfOneThingService",
  components: {
    DsfOneThingBox
  },
  data() {
    return {
      services: [
        {
          name: "服务场景",
          id: 1,
        },
        {
          name: "日常办公",
          id: 2,
        }
      ],
      current: 0,
      items: [[
        {
          name: '录用',
          icon: 'luyong.png',
          ot_cm_id: '612db74b86e849939180999be3b4a650'
        },
        {
          name: '军转干部安置',
          icon: 'zzgbaz.png',
          ot_cm_id: '3e63356161114f53966fcd4dff4c08c5'
        },
        {
          name: '调任',
          icon: 'diaoren.png',
          ot_cm_id: 'e7c4b63fb6ac4eb295d57cc0b3cef155'
        },
        {
          name: '选举登记',
          icon: 'xjdj.png',
          ot_cm_id: "4ce2374870ba4c08a3e67ab741273a5a"
        },
        {
          name: '公开选拔',
          icon: 'gkxb.png',
          ot_cm_id: "72ab01a65cf642bfb5315a7d9aec32b2"
        },
        {
          name: '聘任',
          icon: 'pinren.png',
          ot_cm_id: "d18084ffb3ac4d5694845ca64b117b03"
        },
        {
          name: '参公过渡',
          icon: 'cggd.png',
          ot_cm_id: "04da12fb45184b429ee10c9417c29f16"
        },
        {
          name: '转任',
          icon: 'zhuanren.png',
          ot_cm_id: "cdfaa275d4e047bb9e1a0b71cd0b47ca"
        },
        {
          name: '参公交流',
          icon: 'cgjl.png',
          ot_cm_id: "695d0cc616e64771855f9ada903e90b0"
        },
        {
          name: '职务职级变动',
          icon: 'zwzjbd.png',
          ot_cm_id: "c704f1fb01cb4f91b29bce27a393e710"
        },
        {
          name: '调出',
          icon: 'diaochu.png',
          ot_cm_id: "297153864b9343bfa29fdef8a976590b"
        },
        {
          name: '辞职',
          icon: 'cizhi.png',
          ot_cm_id: "9f3ca651bb0040f9817363b76eafb09e"
        },
        {
          name: '辞退',
          icon: 'citui.png',
          ot_cm_id: "e0102238b3294db4b37f444863619d24"
        },
        {
          name: '开除',
          icon: 'kaichu.png',
          ot_cm_id: "d26ecc86bee74c4494b822c6ac7088a4"
        },
        {
          name: '退休',
          icon: 'zwzjbd.png',
          ot_cm_id: "803f669275114f31991d7db0534dd0cd"
        }
      ],
      // [{
      //   name: '人社局自助经办系统',
      //   icon: 'rsj.png'
      // },
      // {
      //   name: '公积金网上办理',
      //   icon: 'gjj.png'
      // },
      // {
      //   name: '财务预算管理',
      //   icon: 'cwys.png'
      // },
      // {
      //   name: '市学生事务中心官网',
      //   icon: 'sxs.png'
      // },
      // {
      //   name: '市住房困难补贴系统',
      //   icon: 'szf.png'
      // },
      // {
      //   name: '申工通',
      //   icon: 'sgt.png'
      // }]
      ]
    };
  },
  created() {
    this.initList()
  },
  methods: {
    initList() {
      this.$http.get("/meta/list/data?pageNum=1&pageSize=15&namespace=ot.main.daily&pageName=list")
        .then(({ data }) => {
          // 截取数组长度为5条
          this.items[1] = _.map(data.data, (el) => {
            let img = _.isNull(el['ot_main_daily.icon']) ? '' : JSON.parse(el['ot_main_daily.icon']).length > 0 ? JSON.parse(el['ot_main_daily.icon'])[0].relativePath : ''
            return {
              name: el['ot_main_daily.name'],
              icon: dsf.url.getWebPath(img),
              ele: el._id,
              url: _.isNull(el['ot_main_daily.url']) ? '' : el['ot_main_daily.url'],
              redirect_url: _.isNull(el['ot_main_daily.redirect_url']) ? '' : el['ot_main_daily.redirect_url'],
            }
          })
        })
        .catch(() => {
          dsf.layer.message("加载异常", false);
        })
        .finally(() => {});
    },
    handleTag(index, item) {
      this.current = index;
      // console.log("event", item);
    },
    handelEvent(data) {
      if (data.ot_cm_id) {
        this.$openDialog({
          title: "办理事项选择",
          width: '935px',
          height: '540px',
          params: {
            path: `ot/otherpage/sceneanditems?typeid=${data.ot_cm_id}`,
          },
          btns: [
            {
              text: "确定",
              handler: async (res) => {
                const data = await res.yes();
                if (data === false) {
                  return false;
                }
                window.setTimeout(() => {
                  this.$openWindow({
                    dialogArgs: data,
                    url: './page.html#/pc/ot/fillin/form'
                  })
                }, 500)
              },
            },
            {
              text: "取消",
            },
          ],
        })
      }
      if(data.redirect_url){
        this.$openWindow({url: '/login/auth_login?url='+encodeURIComponent(data.redirect_url) })
      }
    }
  },
});
</script>