<template>
  <div id="app">
    <home :is-home="isHome" :is-true="isTrue"></home>
  </div>
</template>

<script>
import home from './views/Home';
const path = ['/pc/ot/otherpage/home'];
export default {
  components: {
    home
  },
  data() {
    return {
      isHome: false,
      themes: dsf.config.defaultTheme,
      isTrue: false, // 根据路由不同切换不一样的头部
    };
  },
  watch: {
    '$route' (to) {
      this.isTrue = path.indexOf(to.fullPath) > -1
    },
  },
  created() {
    let htmlName = window.location.pathname;
    if (htmlName.indexOf("/index.html") >= 0) {
      this.isHome = true;
    }
    this.isTrue = path.indexOf(this.$router.fullPath) > -1
  },
  mounted() {
    //加载皮肤
    document.body.classList.add(this.themes);
  },
  methods: {

  }
};
</script>