<template>
  <div v-show="loaded" class="ds-super-search" :class="{'row-split-boder':superSearchRowBorder}">
    <div v-if="showFitler" class="ds-super-search-row el-row">
      <div class="ds-super-search-item el-col-24 filter-bar">
        <label class="ds-super-search-item-text">筛选:</label>
        <div class="ds-super-search-item-block">
          <div v-for="(item) in getFilterBar()" :key="item.id" class="ds-super-search-filter-item">
            <div class="ds-super-search-filter-item-tag" :class="{'tag-open':item.isFilterExtend}" @click.stop="filterOpen(item)">
              <template v-if="item.type=='enum'">
                <span class="ds-super-search-filter-item-tag-text">{{ item.label }}:</span>
                <template v-if="item.operator!='between'">
                  <span class="ds-super-search-filter-item-value" :title="formatChecked(item)">{{ formatChecked(item) }}</span>
                </template>
                <template v-else>
                  <span class="ds-super-search-filter-item-value" :title="formatBetween(item)">{{ formatBetween(item) }}</span>
                </template>
                <i class="iconfont icon-xia1" style="margin-left: 5px;"></i>
              </template>
              <template v-if="item.type=='text'">
                <span class="ds-super-search-filter-item-tag-text">{{ item.label }}:</span>
                <span class="ds-super-search-filter-item-value" :title="item.values">{{ item.values }}</span>
                <i class="iconfont icon-xia1" style="margin-left: 5px;"></i>
              </template>
              <template v-if="item.type=='datetime'">
                <span class="ds-super-search-filter-item-tag-text">{{ item.label }}:</span>
                <span class="ds-super-search-filter-item-value" :title="formatDate(item)">
                  {{ formatDate(item) }}
                </span>
                <template v-if="item.operator=='between'">
                  <el-date-picker v-model="item.values" :type="(item.showModel=='ymd'?'daterange':'monthrange')" @change="dateChange(item)">
                  </el-date-picker>
                </template>
                <template v-else>
                  <el-date-picker v-model="item.values" :type="(item.showModel=='ymd'?'date':'month')" @change="dateChange(item)">
                  </el-date-picker>
                </template>
              </template>
            </div>
            <div v-if="item.type=='enum'" v-show="item.isFilterExtend" class="ds-ds-super-search-filter-dropdown" @click.stop>
              <el-checkbox-group v-model="item.values">
                <el-checkbox v-for="op in (optionsMap[item.optionKey]||[])" :key="op.value" :label="op.value" @change="checkboxChange($event,item,op,true)">{{ op.text }}</el-checkbox>
              </el-checkbox-group>
              <div v-if="item.isAllowMultipe" style="text-align:center">
                <dsf-button size="small" style="min-width:auto" @click="multipleSure(item)">确定</dsf-button>
                <dsf-button size="small" :type="'auxiliary'" style="min-width:auto" @click="multipleCancel(item)">取消</dsf-button>
              </div>
            </div>
            <div v-if="item.type=='text'" v-show="item.isFilterExtend" class="ds-ds-super-search-filter-dropdown" @click.stop>
              <el-input v-model="item.values" @change="textChange(item)"></el-input>
            </div>
            <span class="item-close" @click="removeFilter(item)"><i class="iconfont icon-shanchu1"></i></span>
          </div>
        </div>
      </div>
    </div>
    <div v-for="(cells,$index) in layout" v-show="!($index>=defaultRowsNumber&&!isExtend)" :key="$index" class="ds-super-search-row el-row">
      <!-- <div class="el-col-"></div> -->
      <div v-for="(item) in cells" :key="item.id" :item-key="item.id" :class="['el-col-'+(item.layout),(item.showModel=='tile'?'tile':'')]" class="ds-super-search-item">
        <label class="ds-super-search-item-text" :title="item.label">{{ item.label }}:</label>
        <div class="ds-super-search-item-block" :class="{'open':item.isExtend}">
          <!--选项-->
          <template v-if="item.type=='enum'">
            <div v-if="item.showModel=='tile'" class="ds-super-search-item-option">
              <div class="ds-super-search-item-option-box" @click.stop>
                <el-checkbox-group v-model="item.values">
                  <el-checkbox v-for="op in (optionsMap[item.optionKey]||[])" :key="op.value" :label="op.value" @change="checkboxChange($event,item,op,true)">{{ op.text }}</el-checkbox>
                  <label v-if="item.operator==='between'" class="el-checkbox">
                    <el-input v-model="item.customerBetween[0]" class="small"></el-input>
                    <span style="margin-left:5px;margin-right:5px">~</span>
                    <el-input v-model="item.customerBetween[1]" class="small"></el-input>
                    <dsf-button style="margin-top:-2px;min-width:auto;margin-left:5px;" size="small" @click="customerSure(item)">确定</dsf-button>
                  </label>
                </el-checkbox-group>
              </div>
              <div v-if="item.isExtend&&item.multiple" style="text-align:center">
                <dsf-button size="small" style="min-width:auto" @click="multipleSure(item)">确定</dsf-button>
                <dsf-button size="small" :type="'auxiliary'" style="min-width:auto" @click="multipleCancel(item)">取消</dsf-button>
              </div>
            </div>
            <div v-else-if="item.showModel=='select'" @click.stop>
              <el-select
                v-if="item.isAllowMultipe"
                v-model="item.values"
                class="indiction-select"
                collapse-tags
                style="width:100%;line-height:40px;"
                filterable
                multiple
                placeholder="请选择"
                @focus="filterAllClose"
                @change="selectChange($event,item)"
              >
                <el-option v-for="op in (optionsMap[item.optionKey]||[])" :key="op.value" :label="op.text" :value="op.value">
                </el-option>
              </el-select>
              <el-select
                v-else
                v-model="item.values[0]"
                class="indiction-select"
                collapse-tags
                style="width:100%;line-height:40px;"
                filterable
                placeholder="请选择"
                @focus="filterAllClose"
                @change="selectChange($event,item)"
              >
                <el-option v-for="op in (optionsMap[item.optionKey]||[])" :key="op.value" :label="op.text" :value="op.value">
                </el-option>
              </el-select>
            </div>
          </template>
          <!--文本框-->
          <template v-if="item.type=='text'">
            <el-input v-model="item.values" :placeholder="'请输入'+(item.label)" @change="textChange(item)"></el-input>
          </template>
          <!--时间-->
          <template v-if="item.type=='datetime'">
            <template v-if="item.operator=='between'">
              <el-date-picker v-if="item.showModel=='ymd'" v-model="item.values" style="width:100%" type="daterange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期" @change="dateChange(item)">
              </el-date-picker>
              <el-date-picker v-else-if="item.showModel=='ym'" v-model="item.values" style="width:100%" type="monthrange" range-separator="至" start-placeholder="开始年月" end-placeholder="结束年月" @change="dateChange(item)">
              </el-date-picker>
            </template>
            <template>
              <el-date-picker v-if="item.showModel=='ymd'" v-model="item.values" style="width:100%" type="date" range-separator="至" placeholder="开始日期" @change="dateChange(item)">
              </el-date-picker>
              <el-date-picker v-if="item.showModel=='ym'" v-model="item.values" style="width:100%" type="month" placeholder="请选择年月" @change="dateChange(item)">
              </el-date-picker>
            </template>
          </template>
        </div>
        <!--平铺模式的按钮-->
        <div v-if="item.showModel=='tile'" class="ds-super-search-item-buttons">
          <template v-if="item.isMore&&!item.isMultipleExtend">
            <dsf-button v-if="!item.isExtend" style="min-width:auto" type="auxiliary" size="small" icon="shuangjiantouxia" @click.stop="extend(item,true)">
              更多
            </dsf-button>
            <dsf-button v-else style="min-width:auto" type="auxiliary" size="small" icon="shuangjiantoushang" @click.stop="extend(item,false)">
              收起
            </dsf-button>
          </template>
          <dsf-button v-if="item.isAllowMultipe&&!item.isMultipleExtend" icon="hao" style="min-width:auto" type="auxiliary" size="small" @click.stop="multipleExtend(item)">
            多选
          </dsf-button>
        </div>
      </div>
    </div>
    <div v-if="hasExtend" class="ds-super-search-extend">
      <span @click="extendHandler">
        {{ !isExtend?"更多筛选条件":"收起筛选条件" }}
        <i class="closearrow iconfont ds-font-6 " :class="{'icon-xia1':!isExtend,'icon-shang1':isExtend}" style="margin-left: 10px;"></i>
      </span>
    </div>
  </div>
</template>
<script>
export default {
  name: 'DsfSuperSearch',
  inheritAttrs: false,
  props: {
    owner: {
      type: Object,
      default() {
        return null;
      }
    },
    searchItemSetting: {
      type: Array,
      default() {
        return []
      }
    },
    searchHeight: {
      type: Number,
      default: 0
    },
    defaultRowsNumber: {
      type: Number,
      default: 2
    },
    showFitler: {
      type: Boolean,
      default: false
    },
    superSearchRowBorder: {
      type: Boolean,
      default: true
    },
    value: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      isExtend: false,
      // hasExtend: false,
      searchItems: [],
      optionsMap: {},
      totalRowsNumber: 0,
      layout: [],
      loaded: false,
      isFirstLoad: false
    }
  },
  computed: {
    totalHeight() {
      if (this.hasExtend && !this.isExtend) {
        let rows = this.initRowsNumber
        return rows * 45 + (this.showFitler ? 45 : 0) + 'px';
      }
      else {
        return "auto";
      }
    },
    hasExtend() {
      if (this.totalRowsNumber > this.defaultRowsNumber) {
        return true;
      }
      return false;
    },
    initRowsNumber() {
      let rows = this.totalRowsNumber < this.defaultRowsNumber ? this.totalRowsNumber : this.defaultRowsNumber;
      return rows;
    },
  },
  watch: {
    searchItemSetting: {
      async handler(v) {
        await this.updateSearchItemSetting();
      },
      immediate: true
    },
    showFitler: {
      handler(v) {
        this.$nextTick(() => {
          this.emitHeight();
        });
      }
    },
    defaultRowsNumber: {
      handler(v) {
        this.$nextTick(() => {
          this.emitHeight();
        });
      }
    }
  },
  created() {
    window.addEventListener("resize", this.resize);
    document.addEventListener("click", this.documentClick);
  },
  mounted() {
    // this.optionsMap = await this.updateData();
    this.totalRowsNumber = this.getTotalRowsNumber();
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.resize);
    document.removeEventListener("click", this.documentClick);
  },
  methods: {
    documentClick() {
      this.filterAllClose();
    },
    filterOpen(item) {
      this.filterAllClose();
      if (item.type == 'enum') {
        item.isExtend = false;
        item.isMultipleExtend = false;
        item.isFilterExtend = !item.isFilterExtend;
        if (item.isAllowMultipe) {
          item.values_bak = dsf.type(item.values) == 'array' ? item.values.slice(0) : item.values;
          item.multiple = true;
        }
      }
      else if (item.type == "text") {
        // item.values_bak = item.values;
        item.isFilterExtend = !item.isFilterExtend;
      }
      this.$nextTick(() => {
        this.emitHeight();
      });
    },
    removeFilter(item) {
      if (item.type == "enum") {
        // if (item.operator == 'between') {
        //   item.values = ""
        // }
        // else {
        //   item.values = []
        // }
        item.values = [];
        item.customerBetween = [];
      }
      else if (item.type == "datetime") {
        if (item.operator == 'between') {
          item.values = [];
        }
        else {
          item.values = ""
        }
      }
      else if (item.type == "text") {
        item.values = ""
      }
      this.postSearch(true);
    },
    filterAllClose() {
      _.each(this.searchItems, (v) => {
        v.isFilterExtend = false;
        v.multiple = false;
      })
    },
    filterClose(item) {
      item.isFilterExtend = false;
      item.multiple = false;
    },
    formatChecked(item) {
      let options = this.optionsMap[item.id];
      if (options) {
        let result = _.filter(options, (v) => item.values.indexOf(v.value) >= 0);
        return _.map(result, "text").join(",")
      }
      return "";
    },
    formatBetween(item) {
      let custom = _.filter(item.customerBetween, (v) => dsf.isDef(v));
      if (custom.length >= 2) {
        return custom[0] + '-' + custom[1];
      }
      else {
        let options = this.optionsMap[item.id];
        if (options) {
          let result = _.filter(options, (v) => item.values.indexOf(v.value) >= 0);
          return _.map(result, "text").join(",");
        }
        return "";
      }
    },
    formatDate(item) {
      let format = item.showModel == 'ym' ? 'yyyy-mm' : 'yyyy-mm-dd';
      if (item.operator == "between") {
        return dsf.date.format(item.values[0], format) + "~" + dsf.date.format(item.values[1], format);
      }
      else {
        return dsf.date.format(item.values, format);
      }
    },
    getFilterBar() {
      let filter = [];
      let value = this.value;
      _.each(this.searchItems, (el) => {
        let val = value ? value[el.field] : null;
        if (dsf.isDef(val)) {
          let v = dsf.type(val) == 'array' ? val : [val];
          v = _.filter(v, (el) => el);
          if (v.length > 0) {
            filter.push(el);
          }
        }
      });
      return filter;
    },
    async updateSearchItemSetting() {
      this.searchItems = _.map(this.searchItemSetting, (el) => {
        // let v = _this.$eval(el.defaultValue, this.owner.$vm);
        this.totalRowsNumber = this.getTotalRowsNumber();
        let defaultValue = null;
        if (!this.owner.isDesign) {
          if (dsf.isDef(el.defaultValue) && el.defaultValue != '') {
            defaultValue = this.owner.$eval(el.defaultValue, this.owner.$vm);
          }
          if (el.searchType == "enum") {
            if (dsf.isDef(defaultValue)) {
              defaultValue = dsf.type(defaultValue) != 'array' ? [defaultValue] : defaultValue;
            }
            else {
              defaultValue = []
            }
            defaultValue = _.map(defaultValue, (v) => v + "")
          }
          else if (el.searchType == 'datetime') {
            if (el.operator == 'between') {
              if (dsf.isDef(defaultValue)) {
                defaultValue = dsf.type(defaultValue) != 'array' ? [defaultValue] : defaultValue;
              }
              else {
                defaultValue = [];
              }
            }
          }
          else {
            defaultValue = dsf.isDef(defaultValue) ? defaultValue + "" : "";
          }
        }

        return {
          //元数据标识
          id: el.id,
          //字段名称
          field: el.field,
          //字段描述文本
          label: el.fieldCaption,
          //高级检索配置内容，比如数据字典等
          valueSource: el.setting,
          //操作符号
          operator: el.operator,
          //检索类型
          type: el.searchType,
          //绑定数据字典的key
          optionKey: el.id || "",
          //用户选择的值
          values: defaultValue,
          //显示方式，文本框、日期等
          showModel: el.model,
          //是否点击了更多展开
          isExtend: false,
          //是否有更多按钮
          isMore: false,
          //是否点击多选后展开
          isMultipleExtend: false,
          //是否允许多选按钮
          isAllowMultipe: el.searchType == 'enum' && el.operator == 'between' ? false : true,
          //是否多选状态
          multiple: false,
          //布局
          layout: el.layout || 24,
          //区间选择是自定义输入内容
          customerBetween: [],
          //是否是筛选条展开
          isFilterExtend: false,
          //字段
          fields: _.filter(el.children, (c) => c.join == 1)
        }
      });
      // console.log(this.searchItems)
      this.optionsMap = await this.updateData();
      // console.log(this.optionsMap)
      this.setLayout();
      this.loaded = true;
      this.$nextTick(() => {
        this.resize();
        if (!this.isFirstLoad) {
          this.isFirstLoad = true;
          let v = this.emitValue();
          this.$nextTick(() => {
            this.$emit("loaded", v);
          });
        }
      });
    },
    getTotalRowsNumber() {
      let total = 0;
      _.each(this.searchItemSetting, (el) => {
        total += ~~el.layout;
      });
      let row_num = Math.ceil(total / 24);
      return row_num;
    },
    setLayout() {
      let matrix = [];
      let count = 0;
      let rowNum = 0;
      _.each(this.searchItems, (el, i) => {
        if (!matrix[rowNum]) {
          matrix[rowNum] = [];
        }
        if (count < 24) {
          matrix[rowNum].push(el);
          count += ~~el.layout;
        }
        let next = i + 1;
        if (this.searchItems[next]) {
          let temp = count + ~~this.searchItems[next].layout;
          if (temp > 24) {
            rowNum++;
            count = 0;
          }
        }
      });
      this.layout = matrix;
    },
    async updateData() {
      if (this.owner.isDesign) {
        return {};
      }
      let optionsMap = {};
      let optionsRequests = [];
      let dictArr = [];
      _.each(this.searchItems, (v) => {
        if (v.type == "enum") {
          if (v.valueSource?.type == "dict") {
            let params = {
              "key": v.id,
              "dictId": v.valueSource.value.key
            }
            dictArr.push(params)
          }
          else if (v.valueSource?.type == "dbsource") {
            let path = v.valueSource.value.key.replace(/\./g, "/");
            optionsRequests.push({
              id: v.id,
              request: this.$webAPI.dbSourceInterface(path, (this.owner?.queryString || {}))
            });
          }
          else if (v.valueSource?.type == "interface") {
            let http = this.$http.get(v.valueSource.value, (this.owner?.queryString || {}));
            optionsRequests.push({
              id: v.id,
              request: http
            });
          }
          else {
            if (v.valueSource && v.valueSource.value) {
              optionsMap[v.id] = v.valueSource.value;
            }
            else {
              optionsMap[v.id] = []
            }
          }
        }
      });
      if (dictArr.length > 0) {
        let params = {
          dicts: dictArr
        }
        optionsRequests.push({
          id: 'dicts',
          request: this.$webAPI.getBatchDict(params)
        })
      }
      try {
        if (optionsRequests.length) {
          let result = await this.$http.all(_.map(optionsRequests, 'request'));
          _.each(result, (res, i) => {
            let id = optionsRequests[i].id;
            if (id == 'dicts') {
              for (let k in res.data) {
                optionsMap[k] = res.data[k];
              }
            }
            else {
              optionsMap[id] = res.data;
            }
          });
        }
        return optionsMap;
      }
      catch (err) {
        // dsf.layer.message("查询检索选项出现错误", false);
        dsf.error('查询检索选项出现错误')
      }
    },
    checkboxChange(v, item, option, isFilter) {
      if (!isFilter) {
        this.filterAllClose();
      }
      if (!item.multiple) {
        if (v) {
          item.values = [option.value];
        } else {
          item.values = [];
        }
        if (item.operator == 'between') {
          item.customerBetween = [];
        }
        this.postSearch()
      }
    },
    selectChange() {
      this.filterAllClose();
      this.postSearch();
    },
    textChange() {
      this.filterAllClose();
      this.postSearch();
    },
    dateChange() {
      this.filterAllClose();
      this.postSearch();
    },
    extend(item, mark) {
      item.isExtend = mark;
      this.$nextTick(() => {
        this.emitHeight();
      })
    },
    customerSure(item) {
      let values = _.filter(item.customerBetween, (v) => v + "");
      if (values.length == 2) {
        item.values = [];
        this.postSearch();
      }
      else {
        dsf.layer.message("请填写" + item.label + "的区间值", false);
      }
    },
    multipleExtend(item) {
      this.filterAllClose();
      if (!item.isExtend) {
        item.isExtend = true;
      }
      item.values_bak = item.values.slice(0);
      item.isMultipleExtend = true;
      item.multiple = true
    },
    multipleSure(item) {
      item.isExtend = false;
      item.multiple = false;
      item.isMultipleExtend = false;
      this.filterAllClose();
      this.postSearch();
    },
    multipleCancel(item) {
      if (item.values_bak) {
        item.values = item.values_bak;
      }
      item.isExtend = false;
      item.multiple = false;
      item.isMultipleExtend = false;
      this.filterAllClose();
    },
    emitValue() {
      let value = {};
      _.each(this.searchItems, (el) => {
        if (el.type == "datetime") {
          let baseformat='yyyy-mm-dd';
          if (el.operator == 'between') {
            let arr = dsf.type(el.values) == 'array' ? el.values : [el.values]
            arr = _.filter(arr, (v) => v);
            value[el.field] = _.map(arr, (v, i) => {
              if (el.showModel == "ym") {
                if (i == 1) {
                  let m = v.getMonth() + 1;
                  let y = v.getFullYear();
                  let d = new Date(y, m, 0);
                  //将结束日期格式化到月最后一天
                  return dsf.date.format(d,baseformat);
                }
              }
              return dsf.date.format(v, baseformat);
            });
          }
          else if (el.operator == "gt" || el.operator == "lteq") {
            if (el.showModel == 'ym') {
              if (el.values) {
                let m = el.values.getMonth() + 1;
                let y = el.values.getFullYear();
                let d = new Date(y, m, 0);
                value[el.field] = el.values ? dsf.date.format(d, baseformat) : "";
              }
              else {
                value[el.field] = ""
              }
            }
            else {
              value[el.field] = el.values ? dsf.date.format(el.values, baseformat) : "";
            }
          }
          else if (el.operator == "gteq" || el.operator == "lt") {
            if (el.showModel == 'ym') {
              if (el.values) {
                let m = el.values.getMonth();
                let y = el.values.getFullYear();
                let d = new Date(y, m, 1);
                value[el.field] = el.values ? dsf.date.format(d, baseformat) : "";
              }
              else {
                value[el.field] = ""
              }
            }
            else {
              value[el.field] = el.values ? dsf.date.format(el.values, baseformat) : "";
            }
          }
        }
        else if (el.type == "enum") {
          if (el.operator == 'between') {
            if (el.customerBetween.length > 0 && el.customerBetween[0] && el.customerBetween[1]) {
              value[el.field] = el.customerBetween;
            }
            else {
              let arr = el.values[0] ? el.values[0].split(";") : "";
              if (arr.length == 2) {
                value[el.field] = arr;
              }
              else if (arr.length == 1) {
                value[el.field] = [arr[0], arr[0]];
              }
              else {
                value[el.field] = [];
              }
            }
          }
          else {
            let arr = dsf.type(el.values) == 'array' ? el.values : [el.values];
            let result = _.filter(arr, (v) => {
              return dsf.isDef(v)
            });
            value[el.field] = result;
          }
        }
        else {
          value[el.field] = el.values;
        }
      })
      this.$emit("update:value", value);
      return value;
    },
    postSearch(istrigger) {
      let value = this.emitValue();
      this.$nextTick(() => {
        this.$emit("change", value, istrigger);
      })
    },
    emitHeight() {
      let height = this.$el.offsetHeight;
      this.$emit("update:searchHeight", height);
    },
    resize() {
      _.each(this.searchItems, (el) => {
        let elm = this.$el.querySelector("[item-key='" + el.id + "']");
        if (elm) {
          if (!el.isExtend) {
            let block = elm.querySelector(".ds-super-search-item-block");
            let optionbox = elm.querySelector(".ds-super-search-item-option");
            if (block && optionbox) {
              let blockHeight = block.offsetHeight;
              let optionHeight = optionbox.offsetHeight;
              if (optionHeight > blockHeight + 10) {
                el.isMore = true;
              }
              else {
                el.isMore = false;
              }
            }
          }
        }
      });
      this.emitHeight();
    },
    extendHandler() {
      this.isExtend = !this.isExtend;
      this.$nextTick(() => {
        this.emitHeight();
      })
    }
  }
}
</script>