<template>
  <div class="ds-login-page">
    <div class="login-header"></div>
    <div class="login-section">
      <div class="login-box">
        <el-tabs v-model="tabname" type="card" @tab-click="handTab">
          <el-tab-pane label="账号密码登录" name="first">
            <el-form @submit.native.prevent>
              <!--<div class="login-box-title">登 录</div>-->
              <div class="login-box-info">
                <el-input
                  v-model="username"
                  :disabled="loading"
                  placeholder="账户名称"
                  size="medium"
                  prefix-icon="el-icon-user-solid"
                ></el-input>
              </div>
              <div class="login-box-info">
                <el-input
                  v-model="password"
                  :disabled="loading"
                  placeholder="密码"
                  size="medium"
                  prefix-icon="el-icon-s-goods"
                  show-password
                ></el-input>
              </div>
              <div class="login-box-button">
                <el-button
                  :loading="loading"
                  type="info"
                  native-type="submit"
                  size="small"
                  @click="submit"
                >
                  登 录
                </el-button>
              </div>
            </el-form>
          </el-tab-pane>
          <el-tab-pane label="法人一证通登录" name="second">
            <el-form @submit.native.prevent>
              <div class="login-box-button">
                <el-button
                  :loading="loading"
                  type="info"
                  native-type="submit"
                  size="small"
                  @click="caLogin"
                >
                  登 录
                </el-button>
              </div>
            </el-form>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
    <div class="login-footer"></div>
  </div>
</template>

<script>
export default {
  name: "Login",
  data() {
    return {
      loading: false,
      username: "",
      password: "",
      tabname: "first",
    };
  },
  created() {
    dsf.storage.session.clear();
  },
  methods: {
    submit() {
      this.username = _.trim(this.username);
      this.password = _.trim(this.password);
      if (this.username === "") {
        dsf.layer.message("账号不能为空！", false);
        return;
      }
      if (this.password === "") {
        dsf.layer.message("密码不能为空！", false);
        return;
      }
      this.login();
    },
    login() {
      this.loading = true;
      this.$webAPI
        .login({
          userName: this.username,
          password: dsf.md5(dsf.md5(this.password)),
          code: "good",
        })
        .then(({ success, message }) => {
          if (success) {
            this.setStateIsLogin();
          } else {
            dsf.layer.message(message, false);
          }
        })
        .catch((err) => {
          dsf.layer.message(err.message || "未知错误", false);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    caLogin() {
      let _this = this;
      window.location.href = 'https://202.96.220.147/oauth2.0/oauth/authorize?response_type=code&scope=read,write&client_id=SWZZ_313&redirect_uri=http://192.168.109.211/dsfa/ca/login&state=2c226823-de86-4a48-9a32-c00393d5996a'     
    },
    // 储存cookie登录状态
    setStateIsLogin() {
      dsf.cookies.set("isLogin", 1);
      let from = this.$route.query.from;
      if (from) {
        from = decodeURIComponent(from);
      } else {
        from = dsf.config.loginSuccessTo;
      }
      location.replace(from);
    },
    handTab() {},
  },
};
</script>