<template>
  <div v-if="isDesign" class="ds-control" style="text-align:center;background-color:#fff;padding:3px 0;font-style:italic;color:#c0c0c0">
    事项选择
  </div>
  <div v-else-if="$route.query.isview != '1'" class="ds-control ds-one-thing-form-select">
    <div class="ds-one-thing-form-select-top" @click="dialogVisible = true">
      <span>事项选择</span>
      <i class="iconfont icon-ln_jiantouyou"></i>
    </div>
    <div class="ds-one-thing-form-select-body">
      <ul>
        <template v-for="({value, text, itemId}, index) in matter">
          <li :key="value" :class="{bofore: activeIndex > index, active: activeIndex === index}">
            <i></i><i></i>
            <a @click="scrollTo(itemId, index)">{{ text }}</a>
          </li>
        </template>
      </ul>
    </div>
    <DsfDialog
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      :modal="loaded"
      :style="{'visibility': loaded ? 'visible' : 'hidden'}"
      title="办理事项选择"
      class="has-button-bar"
      width="935px"
      top="10vh"
      show-close
      append-to-body
      close-on-press-escape
    >
      <div style="position: relative; height: auto; overflow: auto">
        <DsfOneThingSceneSelect ref="content" :meta-data="value" :$vm-props="$vm"></DsfOneThingSceneSelect>
      </div>
      <template v-slot:footer>
        <DsfButton text="取消" type="auxiliary" @click="dialogVisible = false"></DsfButton>
        <DsfButton text="确定" @click="dialogConfirm"></DsfButton>
      </template>
    </DsfDialog>
  </div>
</template>

<script>
import formControl from "_platform/dsf/mixins/formControl";

export default dsf.component({
  ctrlCaption: "办理事项",
  mixins: [formControl],
  name: "DsfOneThingFormSelect",
  props: {
    marginTop: {
      type: Boolean,
      default: false
    },
    marginBottom: {
      type: Boolean,
      default: false
    },
    value: {
      type: Object,
      required: false
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("one-thing-form-select-meta-data");
      }
    }
  },
  data() {
    return {
      loaded: false,
      dialogVisible: false,
      activeIndex: 0,
      showBt: false,
      scene: {
        typeid: '',
        type: ''
      },
      matter: []
    }
  },
  watch: {
    'value'() {
      this.init();
    }
  },
  created () {
    if (!this.isDesign) {
      this.dialogVisible = true;
    }
    this.init();
  },
  mounted() {
    if (!this.isDesign) {
      let $win = $('.ds-page-home-main');
      this.isHome = !!$win.length;
      this.$win = this.isHome ? $win.eq(0) : $('html, body');
      document.body.appendChild(this.$el);
      this.isHome ? this.$win.get(0).addEventListener('scroll', this.onScroll) : window.addEventListener('scroll', this.onScroll);
      window.addEventListener('resize', this.onScroll);
      this.dialogVisible = false;
      setTimeout(() => {
        this.loaded = true;
      }, 1000);
    }
  },
  beforeDestroy(){
    this.$win=null;
  },
  destroyed() {
    this.isHome ? this.$win.get(0).removeEventListener('scroll', this.onScroll) : window.removeEventListener('scroll', this.onScroll);
    window.removeEventListener('resize', this.onScroll);
    this.$el.parentNode.removeChild(this.$el);
  },
  methods: {
    init() {
      if (!this.value) {
        this.$emit('change', {
          type: '',
          typeid: '',
          text: '',
          itemids: '',
          value: ''
        });
        return;
      }
      let { type, typeid, text, itemids, value, code, taskHandleItem } = this.value;
      text = text.split(',');
      itemids = itemids.split(',');
      value = value.split(',');
      code = code.split(',');
      taskHandleItem = taskHandleItem.split(',');
      let matter = [{
        text: '基本信息',
        itemId: 'jiBenXinXi',
        value: '基本信息'
      }];
      value.forEach((v, i) => {
        matter.push({
          text: text[i],
          itemId: itemids[i],
          value: v,
          code: code[i],
          taskHandleItem: taskHandleItem[i]
        });
      })
      this.scene = { typeid, type };
      this.matter = matter;
    },
    dialogConfirm() {
      const data = this.$refs['content'].yes();
      if (data === null) {
        dsf.layer.message('请选择场景事项', false);
        return false;
      }
      this.dialogVisible = false;
      this.emitValueChange(data);
    },
    scrollTo(itemId) {
      if (itemId === '基本信息') {
        this.$win.animate({
          scrollTop: 0
        }, 200, () => {
          this.onScroll();
        });
        return;
      }
      const $el = $(`.ds-section[item-id="${itemId}"]`);
      if ($el.get(0)) {
        let scrollTop = this.isHome ?
          $el.offset().top + this.$win.scrollTop() - this.$win.offset().top :
          $el.offset().top;
        this.$win.animate({
          scrollTop
        }, 200, () => {
          this.onScroll();
        });
      }
    },
    onScroll() {
      const st = this.$win.scrollTop();
      const wh = this.$win.height() - 85;
      const wt = this.$win.offset().top;
      let activeIndex = 0;
      this.matter.forEach(({ itemId }, index) => {
        const $el = $(`.ds-section[item-id="${itemId}"]`);
        if ($el.get(0)) {
          const eh = $el.height();
          const et = this.isHome ? $el.offset().top + st - wt : $el.offset().top;
          if (et + eh > st && et < st + wh && activeIndex < index) {
            activeIndex = index;
          }
        }
      });
      this.activeIndex = activeIndex;
    }
  }
});
</script>
