<template>
  <div class="ds-control ds-form-item ds-cascader" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">
      <span :title="value | proxy(formatter)">{{
        value | proxy(formatter)
      }}</span>
      <div v-if="errors.length > 0" class="ds-error-text">{{ errorsMsg }}</div>
    </template>
    <template v-else>
      <label v-if="showLabel" class="ds-form-label" :style="getLabelWidthStyle()">
        {{ label }}
        <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
      </label>
      <div class="ds-form-block" :style="getFormItemBlockMarginLeft()">
        <el-cascader
          v-if="!readOnly"
          v-model="inputValue"
          :options="items"
          :disabled="disabled"
          :filterable="filterable"
          :placeholder="placeholder"
          :clearable="clearable"
          :props="props"
          @change="change"
          @blur="blur"
        ></el-cascader>
        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <span>{{ value | proxy(formatter) }}</span>
        </div>
        <div v-if="errors.length > 0" class="ds-error-text">
          {{ errorsMsg }}
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import formControl from "../../../mixins/formControl";

const options = [
  {
    value: "1",
    label: "指南",
    children: [
      {
        value: "1-1",
        label: "设计原则",
        children: [
          {
            value: "1-1-1",
            label: "一致",
          },
          {
            value: "1-1-2",
            label: "反馈",
          },
          {
            value: "1-1-3",
            label: "效率",
          },
          {
            value: "1-1-4",
            label: "可控",
          },
        ],
      },
      {
        value: "1-2",
        label: "导航",
        children: [
          {
            value: "1-2-1",
            label: "侧向导航",
          },
          {
            value: "1-2-2",
            label: "顶部导航",
          },
        ],
      },
    ],
  },
  {
    value: "2",
    label: "组件",
    children: [
      {
        value: "2-1",
        label: "Basic",
        children: [
          {
            value: "2-1-1",
            label: "Layout 布局",
          },
          {
            value: "2-1-2",
            label: "Color 色彩",
          },
          {
            value: "2-1-3",
            label: "Typography 字体",
          },
          {
            value: "2-1-4",
            label: "Icon 图标",
          },
          {
            value: "2-1-5",
            label: "Button 按钮",
          },
        ],
      },
      {
        value: "2-2",
        label: "Form",
        children: [
          {
            value: "2-2-1",
            label: "Radio 单选框",
          },
          {
            value: "2-2-2",
            label: "Checkbox 多选框",
          },
          {
            value: "2-2-3",
            label: "Input 输入框",
          },
          {
            value: "2-2-4",
            label: "InputNumber 计数器",
          },
          {
            value: "2-2-5",
            label: "Select 选择器",
          },
          {
            value: "2-2-6",
            label: "Cascader 级联选择器",
          },
          {
            value: "2-2-7",
            label: "Switch 开关",
          },
          {
            value: "2-2-8",
            label: "Slider 滑块",
          },
          {
            value: "2-2-9",
            label: "TimePicker 时间选择器",
          },
          {
            value: "2-2-10",
            label: "DatePicker 日期选择器",
          },
          {
            value: "2-2-11",
            label: "DateTimePicker 日期时间选择器",
          },
          {
            value: "2-2-12",
            label: "Upload 上传",
          },
          {
            value: "2-2-13",
            label: "Rate 评分",
          },
          {
            value: "2-2-14",
            label: "Form 表单",
          },
        ],
      },
      {
        value: "2-3",
        label: "Data",
        children: [
          {
            value: "2-3-1",
            label: "Table 表格",
          },
          {
            value: "2-3-2",
            label: "Tag 标签",
          },
          {
            value: "2-3-3",
            label: "Progress 进度条",
          },
          {
            value: "2-3-4",
            label: "Tree 树形控件",
          },
          {
            value: "2-3-5",
            label: "Pagination 分页",
          },
          {
            value: "2-3-6",
            label: "Badge 标记",
          },
        ],
      },
      {
        value: "2-4",
        label: "Notice",
        children: [
          {
            value: "2-4-1",
            label: "Alert 警告",
          },
          {
            value: "2-4-2",
            label: "Loading 加载",
          },
          {
            value: "2-4-3",
            label: "Message 消息提示",
          },
          {
            value: "2-4-4",
            label: "MessageBox 弹框",
          },
          {
            value: "2-4-5",
            label: "Notification 通知",
          },
        ],
      },
      {
        value: "2-5",
        label: "Navigation",
        children: [
          {
            value: "2-5-1",
            label: "NavMenu 导航菜单",
          },
          {
            value: "2-5-2",
            label: "Tabs 标签页",
          },
          {
            value: "2-5-3",
            label: "Breadcrumb 面包屑",
          },
          {
            value: "2-5-4",
            label: "Dropdown 下拉菜单",
          },
          {
            value: "2-5-5",
            label: "Steps 步骤条",
          },
        ],
      },
      {
        value: "2-6",
        label: "Others",
        children: [
          {
            value: "2-6-1",
            label: "Dialog 对话框",
          },
          {
            value: "2-6-2",
            label: "Tooltip 文字提示",
          },
          {
            value: "2-6-3",
            label: "Popover 弹出框",
          },
          {
            value: "2-6-4",
            label: "Card 卡片",
          },
          {
            value: "2-6-5",
            label: "Carousel 走马灯",
          },
          {
            value: "2-6-6",
            label: "Collapse 折叠面板",
          },
        ],
      },
    ],
  },
  {
    value: "3",
    label: "资源",
    children: [
      {
        value: "3-1",
        label: "Axure Components",
      },
      {
        value: "3-2",
        label: "Sketch Templates",
      },
      {
        value: "3-3",
        label: "组件交互文档",
      },
    ],
  },
];

export default dsf.component({
  name: "DsfCascader",
  mixins: [formControl],
  ctrlCaption: "下拉框级联",
  props: {
    simple: {
      type: Boolean,
      default: false,
    },
    placeholder: {
      type: String,
      default: "请选择",
    },
    // 值
    value: {
      type: [Object, Array],
      required: false
    },
    // 选项
    items: {
      type: Array,
      default() {
        return [];
      }
    },
    filterable: {
      type: Boolean,
      default: false,
      desc: "是否可搜索"
    },
    clearable: {
      type: Boolean,
      default: true,
      desc: "是否支持选项清空"
    },
    showAllLevels: {
      type: Boolean,
      default: true,
      desc: "输入框中是否显示选中值的完整路径"
    },
    collapseTags: {
      type: Boolean,
      default: false,
      desc: "多选模式下是否折叠Tag"
    },
    // props
    expandTrigger: {
      type: String,
      default: 'click',
      desc: "次级菜单的展开方式 click / hover"
    },
    multiple: {
      type: Boolean,
      default: false,
      desc: "是否多选"
    },
    checkStrictly: {
      type: Boolean,
      default: false,
      desc: "是否可选择任意一级"
    },
    propsValue: {
      type: String,
      default: 'value',
      desc: "指定选项的值为选项对象的某个属性值"
    },
    propsLabel: {
      type: String,
      default: 'text',
      desc: "指定选项标签为选项对象的某个属性值"
    },
    propsChildren: {
      type: String,
      default: 'children',
      desc: "指定选项的子选项为选项对象的某个属性值"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("items-meta-data");
      },
    },
  },
  data() {
    return {
      valueCopy: null
    };
  },
  computed: {
    props() {
      return {
        expandTrigger: this.expandTrigger === 'click' ? 'click' : 'hover',
        checkStrictly: this.checkStrictly,
        multiple: this.multiple,
        value: this.propsValue,
        label: this.propsLabel,
        children: this.propsChildren
      }
    },
    $items() {
      let items = this.items || []
      //如果选项比值后获取到的情况下，重新更新一下值
      this.initValue(this.value);
      return items;
    },
    inputValue: {
      get() {
        if (dsf.isUnDef(this.value)) {
          return this.multiple ? [] : null;
        }
        if (this.multiple) {
          if (dsf.type(this.value) == "array") {
            return this.value.map(v => v.value.split('/'));
          } else if (dsf.type(this.value) == "object") {
            return [this.value.value.split('/')];
          }
        } else {
          if (dsf.type(this.value) == "array") {
            return this.value[0].value.split('/');
          } else if (dsf.type(this.value) == "object") {
            return this.value.value.split('/');
          }
        }
        return this.multiple ? [] : null;
      },
      set(v) {
        if (!v || !v.length) {
          this.emitValueChange(this.multiple ? [] : null);
          return;
        }
        if (!this.multiple) {
          let val = this.getValueByPath(v);
          this.emitValueChange(val);
        } else {
          let val = v.map(v2 => this.getValueByPath(v2));
          this.emitValueChange(val);
        }
      }
    }
  },
  methods: {
    getValueByPath(path) {
      let text = [], value = [];
      let items = this.items;
      for (let i = 0; i < path.length; i++) {
        let p = path[i];
        if (items == null) {
          return null;
        } else {
          for (let i = 0; i < items.length; i++) {
            let item = items[i];
            if (item[this.propsValue] === p) {
              value.push(p);
              text.push(item[this.propsLabel]);
              if (item[this.propsChildren]) {
                items = item[this.propsChildren];
              } else {
                items = null;
              }
              break;
            }
          }
        }
      }
      return {
        text: text.join('/'),
        value: value.join('/')
      }
    },
    change() {
      this.$dispatch("change", this.value);
      this.$nextTick(() => {
        this.emitFormValidate(this.value);
      })
    },
    blur(){
      this.$dispatch("blur");
    }
  },
});
</script>
