<template>
  <ul class="ds-control ds-no-padding ds-one-thing-steps">
    <li
      v-for="(item, index) in stepList"
      :key="index"
      :class="{ active: active === index, old: active > index }"
      :style="{ width: stepWidth }"
    >
      <div class="ds-one-thing-steps-icon success">
        <span v-if="active > index" class="iconfont icon-duigoux"></span>
        <span v-else>{{ index + 1 }}</span>
      </div>
      <div class="ds-one-thing-steps-info">
        <div class="ds-one-thing-steps-name">
          {{ item.name }}
          <el-popover
            v-if="item.msg.length"
            :content="item.msg"
            placement="bottom"
            width="240"
            trigger="hover"
          >
            <i slot="reference" class="iconfont icon-bangzhuzhongxin"></i>
          </el-popover>
        </div>
        <div class="ds-one-thing-steps-other">
          <div v-for="(chil, inddex2) in item.children" :key="inddex2">
            <span v-if="chil.status !== ''">{{ chil.status }}</span>
            <span v-if="chil.date !== ''">{{ chil.date }}</span>
          </div>
        </div>
      </div>
    </li>
  </ul>
</template>

<script>
import control from "_platform/dsf/mixins/control";

export default dsf.component({
  name: "DsfOneThingSteps",
  ctrlCaption: "步骤条",
  mixins: [control],
  props: {
    // 单个步骤的宽度
    stepWidth: {
      type: String,
      default: "300px",
    },
  },
  data() {
    return {
      loading: false,
      active: 2,
      stepConfig: [
        {
          name: "申请",
          replace: ['申请']
        },
        {
          name: "受理",
          replace: ['预审', '受理']
        },
        {
          name: "办结",
          replace: ['办理']
        },
      ],
      stepList: [],
      controlsCopy: []
    };
  },
  watch: {
    '$vm.isView' (to) {
      this.init(to);
    }
  },
  created() {
    if (!this.isDesign) {
      this.postData();
    } else {
      this.stepList = [
        { name: "申请", children: [{date: "2020-10-8", status: ""}], msg: "" },
        { name: "受理", children: [{date: "2020-10-11 18:06:35", status: "受理"}], msg: "" },
        { name: "完成", children: [{date: "", status: ""}], msg: "" },
      ];
    }
  },
  mounted() {
    if (!this.isDesign && this.$vm && this.$vm.isView) {
      this.init(this.$vm.isView);
    }
  },
  methods: {
    init(isView) {
      if (isView != '1') {
        this.controlsCopy.forEach(v => {
          v.visible = false;
        });
      } else if (!this.controlsCopy.length) {
        for (let k in this.$vm.$controls) {
          let v = this.$vm.$controls[k];
          if (v.isFormControl && !v.visible) {
            this.controlsCopy.push(v);
            v.visible = true;
          }
        }
      } else {
        this.controlsCopy.forEach(v => {
          v.visible = true;
        });
      }
    },
    postData() {
      this.loading = true;
      this.$http
        .get("/uapply/getTrack", {
          id: this.$route.query['id'] || '',
          applyno: this.$route.query['applyno'] || ''
        })
        .done(({ success, data }) => {
          if (success) {
            this.postDataSuccess(data);
          } else {
            dsf.layer.message("加载异常", false);
          }
        })
        .error(() => {
          dsf.layer.message("加载异常", false);
        })
        .always(() => {
          this.loading = false;
        });
    },
    postDataSuccess(data) {
      let obj = {};
      data.forEach(v => {
        obj[v.nodename] = v;
      });
      let l = this.stepConfig.length;
      this.stepList = this.stepConfig.map(({name, replace}, i) => {
        let item = null;
        let children = [];
        replace.forEach(v => {
          item = obj[v];
          if (item) {
            children.push({
              date: item.optime || "",
              status: item.applystate || ""
            });
          }
        })
        if (item) {
          this.active = i;
        }
        return {
          name,
          msg: (item && i + 1 == l) ? item.suggestion : '',
          children
        }
      });
    },
  },
});
</script>
