<template>
  <div class="ds-control ds-form-item one-thing-upload" :class="getCss">
    <div class="upload-list">
      <div class="a">
        <div v-if="showLabel" class="ds-form-label text-label" :title="label" :class="{ 'line-height' : label.length <= 38}">
          {{ label }} 
          <i v-if="dataCapion" class="iconfont icon-bangzhuzhongxin ds-control-helper" @click="showHelper"></i>
        </div>
      </div>
      <div class="b">
        <el-upload
          ref="upload"
          class="upload-file-button"
          :disabled="readOnly"
          :accept="uploadFileExts"
          :multiple="multiple"
          :headers="headersParam"
          :limit="parseInt(uploadFileNum)"
          :action="setRequestUrl"
          :show-file-list="false"
          :before-upload="beforeUpload"
          :on-success="handleSuccess"
          :on-error="handleError"
          :on-exceed="handleExceed"
          :auto-upload="autoUpload"
        >
          <template v-if="multipleFiles && multipleFiles.length > 0">
            <div>已上传/{{ needSignature? (signatureStatus == null ? '需要验签' : (signatureStatus ? '验签通过': '验签不通过') ): '无需验签' }}</div>
          </template>
          <template v-else>
            <div>未上传/{{ needSignature? (signatureStatus == null ? '需要验签' : (signatureStatus ? '验签通过': '验签不通过') ): '无需验签' }}</div>
          </template>
        </el-upload>
      </div>
      <div class="c">
        <div v-if="tagsList.length > 0" class="one-thing-list-tags">
          <span v-for="(item, idx) in tagsList" :key="idx">{{ item }}</span>
        </div>
      </div>
      <div class="d">
        <div class="right-file-button">
          <div :class="{ 'disabled-icon': (multipleFiles.length == 0 || (multipleFiles.length > 0 && needSignature && !signatureStatus)) }">
            <i class="operation-icon el-icon-view" title="查看" @click="handleColumnClick('view', 0)"></i>
            <i class="operation-icon el-icon-download" title="下载" @click="handleColumnClick('download', 0)"></i>
          </div>  
          <div>
            <i v-show="needCreateTable && formId" class="operation-icon el-icon-menu" @click="createTable"></i>
            <i v-show="needCreatePdf" class="operation-icon el-icon-picture" @click="handleMuliple"></i>
          </div>
          <div :class="{ 'disabled-icon': multipleFiles.length == 0 }">
            <i
              v-if="!readOnly"
              title="删除"
              class="operation-icon el-icon-delete"
              @click="handleColumnClick('delete', 0)"
            ></i>
          </div>
          <el-upload
            v-if="!readOnly"
            class="list-upload"
            :accept="uploadFileExts"
            :headers="headersParam"
            :multiple="multiple"
            :limit="parseInt(uploadFileNum)"
            :action="setRequestUrl"
            :show-file-list="false"
            :before-upload="beforeUpload"
            :on-success="handleSuccess"
            :on-error="handleError"
            :on-exceed="handleExceed"
            :auto-upload="autoUpload"
          >
            <i class="operation-icon el-icon-upload2" @click="handleColumnClick('upload2', 0)"></i>
          </el-upload>
        </div>
      </div>
    </div>
    <div v-if="errors.length>0 && multipleFiles.length <= 0" class="ds-error-text">{{ errorsMsg }}</div>
    <el-dialog title="多图上传" :visible.sync="uploadVisible" center>
        <el-row style="margin-bottom: 10px;" type="flex" justify="end"> 
          <el-button type="primary" @click="generatePdf">生成pdf</el-button>
        </el-row>
        <el-row>
        <el-upload
          ref="uploadImage"
          :action="setRequestUrl"
          :multiple="multiple"
          :file-list="fileList"
          list-type="picture-card"
          :accept="uploadFileExts"
          :headers="headersParam"
          :before-upload="beforeUpload"
          :before-remove="removeFile"
          :on-success="handleSuccess"
          :on-error="handleError"
          :on-exceed="handleExceed">
          <i class="el-icon-plus"></i>
        </el-upload>
        </el-row>
      </el-dialog>  
  </div>
</template>

<script>
import formControl from "_platform/dsf/mixins/formControl";
export default dsf.component({
  name: "DsfOneThingUploadFile",
  mixins: [formControl],
  ctrlCaption: "文件上传",
  props: {
    simple: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      default: "文件上传",
    },
    btnName: {
      type: String,
      default: "请点击上传文件",
    },
    needSignature: {
      type: Boolean,
      default: true
    },
    needGetImg4Online: {
      type: Boolean,
      default: true
    },
    needCreateTable: {
      type: Boolean,
      default: false
    },
    needCreatePdf: {
      type: Boolean,
      default: true
    },
    needOverGet: {
      type: Boolean,
      default: true
    },
    showBtnName: {
      type: Boolean,
      default: true,
    },
    multiple: {
      type: Boolean,
      default: false,
    },
    uploadFileNum: {
      type: [Number, String],
      default: 20,
    },
    maxFileSize: {
      type: String,
      default: "",
    },
    generateFormUrl: {
      type: String,
      default: ""
    },
    zipFileName: {
      type: String,
      default: "",
    },
    required: {
      type: Boolean,
      default: false,
    },
    requestUrl: {
      type: String,
      default: "",
    },
    extsSetting: {
      type: Object,
      default() {
        return {};
      },
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("file-meta-data");
      },
    },
    hideBtns: {
      type: Boolean,
      default() {
        return false;
      },
    },
    // 二次开发绑定值 {":tags":"['教育部', '出入证-办理', '工资-新进人员']"}
    tags: {
      type: [Object, Array],
      default: null
    },
    certificate: {
      type: String,
      default: "",
    }
  },
  data() {
    return {
      fileList: [],
      autoUpload: true,
      uploadVisible: false,
      signatureStatus: null,
      generatePdfStatus: null,
      isShowDownloadBtn: false, // 是否显示下载按钮
      uploadFileType: "file", //默认上传类型
      uploadFileExts: "", //允许上传后缀名
      acceptMime: "", // 弹窗筛选条件MiMe类型
      headersParam: {
        "X-XSRF-TOKEN": dsf.getCookie("XSRF-TOKEN") || "",
      },
      uploadFile: {
        status: false, // 当前为重新上传
        index: 0, // 当前索引
      }
    };
  },
  computed: {
    tagsList () {
      return _.isArray(this.tags) ? this.tags : []
    },
    setRequestUrl() {
      return dsf.url.getWebPath(this.requestUrl || "file/upload/");
    },
    multipleFiles: {
      get() {
        let files = [];
        if (
          this.value !== "" &&
          dsf.isDef(this.value) &&
          typeof this.value === "string"
        ) {
          files = JSON.parse(this.value);
        }
        return files;
      },
      set(v) {
        this.emitValueChange(JSON.stringify(v));
      },
    },
    signature(){
      return this.needSignature? 1: 0;
    },
    formId: {
      get() {
        return this.$route.query.id;
      },
      set(v) {

      }
    }
  },
  created() {
    this.rebuildDialogData();
  },
  mounted() {
    this.formId = this.$route.query.id; 
    if (this.needGetImg4Online && this.certificate) {
      this.getDeriveCertFile(this.certificate)
    }
  },
  methods: {
    beforeUpload(file) {
      this.signatureStatus = null
      // 判断文件格式
      if (this.uploadFileExts) {
        let testmsg = file.name.split(".")[1].toLowerCase();
        if (!this.uploadFileExts.includes(testmsg)) {
          dsf.layer.message(
            `选择文件中包含不支持的文件格式,请选择包含这些格式的文件：${this.uploadFileExts}`,
            false
          );
          return false;
        }
      }
      // 判断文件大小
      let maxSize = this.maxFileSize ? this.getFileSize(this.maxFileSize) : "";
      let isMaxSize = maxSize ? file.size / 1024 < maxSize : true;
      if (!isMaxSize) {
        dsf.layer.message(`文件大小不能超过${this.maxFileSize}`, false);
        return false;
      }
      // 是否有重名
      let hasSameName = false,
        repeateName = "";
      // 上传的文件与已上传的文件名对比
      _.forEach(this.multipleFiles, (item) => {
        if (item.originalFileName == file.name) {
          hasSameName = true;
          repeateName += item.name + " ";
        }
      });
      if (hasSameName) {
        dsf.layer.message(
          `已存在文件【${repeateName}】，请修改后重新上传`,
          false
        );
        return false;
      }
    },
    //上传成功
    handleSuccess(response, file, fileList) {
      this.loading = false;
      if (response.success && response.state == 20000) {
        let files = response.data[0];
        if (this.needSignature) {
          this.handSignature(response.data[0].relativePath);
        }
        // 只限制一个文件
        this.multipleFiles.push(files);
        this.emitValueChange(JSON.stringify(this.multipleFiles));
        if (files["contentType"].startsWith("image")) {
          this.fileList.push({
            id: files.id,
            name: files.originalFileName,
            url: files.absolutePath,
            response: response,
            status: 'success'
          })
        }
        console.info(JSON.stringify(this.multipleFiles));
        dsf.layer.message('上传成功');
      } else {
        dsf.layer.message(response.message, false);
      }
    },
    //上传失败
    handleError(err, file, fileList) {
      this.loading = false;
      let errData = JSON.parse(JSON.stringify(err));
      if (errData.status == "404") {
        dsf.layer.message("服务器异常", false);
      } else {
        dsf.layer.message("上传异常", false);
      }
    },
    handleExceed(files, fileList) {
      // 同时最多只能上传数量只能为
      dsf.layer.message(`当前限制选择最多 ${this.uploadFileNum} 个文件`, false);
    },
    //获取文件大小
    getFileSize(data) {
      let fileSize = parseInt(data);
      if (data.indexOf("M") > -1 || data.indexOf("m") > -1) {
        fileSize = parseInt(data) * 1024;
      }
      return fileSize;
    },
    handleColumnClick(type, index) {
      const handler = {
        view: () => {
          if (this.multipleFiles[0]) {
            let root = dsf.url.getWebPath("preview/file");
            const preUrl = dsf.config.file.preViewServer;
            const relativePath = this.multipleFiles[0].relativePath;
            let url = `${root}?url=${preUrl}${relativePath}`;
            window.open(url);
          }
        },
        delete: () => {
          if (this.multipleFiles[0]) {
            dsf.layer
              .confirm(
                this.multipleFiles.length > 1 ? '确定要删除所有文件吗?' : `确定要删除【${this.multipleFiles[0].originalFileName}】文件吗?`
              )
              .then(() => {
                // 删除重新再上传？ this.$refs.upload
                this.$refs.upload.uploadFiles.length = 0;
                this.$refs.uploadImage.uploadFiles.length = 0;
                this.fileList.length = 0;
                // 删除行
                this.multipleFiles.length = 0;
                this.emitValueChange(JSON.stringify(this.multipleFiles));
                this.signatureStatus = null;  
                this.generatePdfStatus = null;
              })
              .catch((e) => e);
          }
        },
        download: () => {
          if (this.multipleFiles[0]) {
            var url = dsf.url.getWebPath("file/download");
            $(
              '<form action="' +
              url +
              '" method="get">' + // action请求路径及推送方法
              '<input type="text" name="files" value="' +
              this.multipleFiles[0].id +
              '"/>' + // id^fileName
              '<input type="text" name="zipName" value="' +
              (this.multipleFiles[0].fileName || "") +
              '"/>' + // 压缩包名称
                "</form>"
            )
              .appendTo("body")
              .submit()
              .remove();
          }
        },
        upload2: () => {
            this.uploadFile = {
                status: true, // 当前为重新上传
                index: index, // 当前索引
            };
        },
      };
      handler[type] && handler[type]();
    },
    rebuildDialogData() {
      if (this.extsSetting.extStr) {
        let obj = this.extsSetting;
        // this.uploadFileType = 'file';//默认上传类型
        // this.uploadFileExts=""//允许上传后缀名
        // this.acceptMime =""//弹窗筛选条件MiMe类型
        this.uploadFileType = obj.fileType;
        this.isLinkeMine = obj.isLinkeMine;
        this.uploadFileExts = obj.extStr;
        this.acceptMime = obj.mineStr;
        // console.log("this.uploadFileExts", this.uploadFileExts);
        // console.log("this.acceptMime", this.acceptMime);
      }
    },
    handSignature(path) {
      this.$http.get('ca/verify', {
        relativePath: path
      }).done(({ success, data }) => {
        if (success) {
          if (data) {
            this.signatureStatus = true
          } else {
            this.signatureStatus = false
          }
        }
      }).always(() => {
        
      })
    },
    getDeriveCertFile(certificate) {
      let _this = this
      this.$http.get('ca/getDeriveCertFile', {
        certificate: certificate
      }, {
        timeout: 300000
      }).done(({ success, data }) => {
        if (success) {
          if (data && data.length > 0) {
            _this.multipleFiles.splice(0, 1, data[0]);
            _this.emitValueChange(JSON.stringify(data));
          } 
        }
      }).always(() => {
        
      })
    },
    createTable() {
      let _this = this
      window.location.href = _this.generateFormUrl + '&id=' + this.$route.query.id;
    },
    generatePdf() {
      let _this = this
      let paths = '';
      _this.uploadVisible = false;
      this.multipleFiles.forEach((item, index) => {
        if (item["contentType"].startsWith("image")) {
          paths += item.relativePath;
          if (index < this.multipleFiles.length - 1) {
            paths += ';';
          }
        }
      }) 
      this.$http.get('ca/generatePdf', {
        paths: paths
      }, {
        timeout: 300000
      }).done(({ success, data }) => {
        if (success) {
          if (data && data.length > 0) {
            _this.multipleFiles.splice(0, 1, data[0]);
            _this.emitValueChange(JSON.stringify(data));
            _this.generatePdfStatus = true;
          } 
        }
      }).always(() => {
        
      })
    },
    handleMuliple() {
      let _this = this
      _this.uploadVisible = true;
    },
    removeFile(file, fileList) {
      console.info(file);
      let _this = this
      let index = _this.multipleFiles.findIndex((item) => {
        if (file.response) {
          return item.id == file.response.data[0].id
        }
      })
      if (file.response) {
        _this.multipleFiles.splice(index, 1);
      }
      index = _this.fileList.findIndex((item) => {
        return item.id == file.id
      })
      _this.fileList.splice(index, 1);
    } 
  }  
});
</script>