<template>
  <div class="ds-control ds-form-item ds-area-select" :class="getCss">
    <template v-if="simple && readOnly && !isDesign">{{ getShowContent() }}</template>
    <template v-else>
      <label
        v-if="showLabel"
        class="ds-form-label"
        :style="getLabelWidthStyle()"
      >
        {{ label }}
        <i
          v-if="dataCapion"
          class="iconfont icon-bangzhuzhongxin ds-control-helper"
          @click="showHelper"
        ></i>
      </label>
      <div
        class="ds-form-block"
        :class="{'ds-error-border':errors.length>0}"
        :style="getFormItemBlockMarginLeft()"
      >
        <template v-if="!readOnly">
          <div v-if="displayMode === 1" class="ds-area-select-wrap">
            <el-select
              v-if="showProvince"
              v-model="province"
              :no-data-text="noDataTextProvince"
              :style="{ width: itemWidth }"
              value-key="areacode"
              :disabled="isDisabled(provinceList)"
              placeholder="请选择省"
              @change="onChange($event, 1)"
            >
              <el-option
                v-for="el in provinceList"
                :key="el.areacode"
                :label="el.name"
                :value="el"
              ></el-option>
            </el-select>
            <el-select
              v-if="showCity"
              v-model="city"
              :no-data-text="noDataTextCity"
              :style="{ width: itemWidth }"
              value-key="areacode"
              :disabled="isDisabled(cityList)"
              placeholder="请选择市"
              @change="onChange($event, 2)"
            >
              <el-option
                v-for="el in cityList"
                :key="el.areacode"
                :label="el.name"
                :value="el"
              ></el-option>
            </el-select>
            <el-select
              v-if="showDist"
              v-model="dist"
              :no-data-text="noDataTextDist"
              :style="{ width: itemWidth }"
              value-key="areacode"
              :disabled="isDisabled(distList)"
              placeholder="请选择县/区"
              @change="onChange($event, 3)"
            >
              <el-option
                v-for="el in distList"
                :key="el.areacode"
                :label="el.name"
                :value="el"
              ></el-option>
            </el-select>
            <el-select
              v-if="showStreet"
              v-model="street"
              :no-data-text="noDataTextStreet"
              :style="{ width: itemWidth }"
              value-key="areacode"
              :disabled="isDisabled(streetList)"
              placeholder="请选择街道"
              @change="onChange($event, 4)"
            >
              <el-option
                v-for="el in streetList"
                :key="el.areacode"
                :label="el.name"
                :value="el"
              ></el-option>
            </el-select>
            <el-select
              v-if="showTown"
              v-model="town"
              :no-data-text="noDataTextTown"
              :style="{ width: itemWidth }"
              value-key="areacode"
              :disabled="isDisabled(townList)"
              placeholder="请选择村居"
              @change="onChange($event, 5)"
            >
              <el-option
                v-for="el in townList"
                :key="el.areacode"
                :label="el.name"
                :value="el"
              ></el-option>
            </el-select>
          </div>

          <div v-else-if="displayMode === 2">
            <el-cascader
              ref="elCascader"
              v-model="caValue"
              v-loading="caLoading"
              :options="caOptions"
              separator="-"
              :props="caProps"
              filterable
              @change="caChange"
            ></el-cascader>
          </div>
        </template>

        <!--只读状态只显示div-->
        <div v-else-if="readOnly" class="ds-form-readonly">
          <span>{{ getShowContent() }}</span>
        </div>
        <div v-if="errors.length>0" class="ds-error-text">{{ errorsMsg }}</div>
      </div>
    </template>
  </div>
</template>

<script>
  import formControl from "../../../mixins/formControl";

  const config1 = {
    0: 'province',
    1: 'city',
    2: 'dist',
    3: 'street',
    4: 'town'
  }
  const config2 = [[0, 3, '000000000'], [0, 5, '0000000'], [0, 7, '00000'], [0, 9, '000'], [0, 12, '']];

  export default dsf.component({
    name: "DsfAreaSelect",
    mixins: [formControl],
    ctrlCaption: "行政区划",
    props: {
      placeholder: {
        type: String,
        default: "请选择",
      },
      value: {
        type: Object,
        default() {
          return {};
        },
      },
      superCode: {
        type: [String, Number],
        default: "000000000000",
      },
      fixedCode: {
        type: String,
        default: "",
      },
      range: {
        type: Array,
        default() {
          return ["0", "4"];
        },
      },
      metadata: {
        type: Object,
        default() {
          return dsf.metadata.get("items-meta-data");
        },
      },
      //展示方式 1内联 | 2级联
      displayMode: {
        type: Number,
        default: 1
      },
      // 是否自动选择下一级第一个
      autoSelect: {
        type: Boolean,
        default: true
      }
    },
    data() {
      return {
        //当displayMode=1 时需要用到的属性
        loading: false,
        province: {},
        city: {},
        dist: {},
        street: {},
        town: {},
        data: [],

        //当displayMode=2 时需要用到的属性
        caOptions: [],
        caProps: {
          value: "areacode",
          label: "name",
          emitPath: false
        },
        caValue: {},
        reference: {},   //用于判断value是内部改变还是外部改变
        caLoading: false
      };
    },
    watch: {
      superCode(v) {
        if (v && v.length === 12) {
          const [level, gid] = this.getLevelAndGlobalid(v);
          if (level) {
            this.$emit("update:range", [
              String(level - 2),
              String(this.endRange),
            ]);
          }
        }
      },
      'value'(to, from) {
        if (!to || !to.text || !to.value || !!from) return;

        if (this.displayMode === 1){
          this.valueInit(to).then(({data, vIds}) => {
            this.valueInitNext(data, vIds);
          });
        }
        else if (this.displayMode === 2){
          if (this.reference.text === to.text) return;
          this.caGetValueList()
        }

      }
    },
    created() {
      if (this.isDesign) return;

      this.reference = {};

      //如果展示方式为内联展示
      if (this.displayMode === 1) {
        let val = this.value,
          vData = null,
          vvIds = null,
          type = false;
        this.loadData().then(() => {
          if (!vData) {
            type = true;
          } else {
            this.valueInitNext(vData, vvIds);
          }
        });

        if (val && val.text && val.value) {
          this.valueInit(val).then(({data, vIds}) => {
            if (!type) {
              vData = data;
              vvIds = vIds;
            } else {
              this.valueInitNext(data, vIds);
            }
          });
        }

      }
      //如果展示方式为级联展示
      else if (this.displayMode === 2) {
        this.caGetDefaultList()
      }
    },
    computed: {
      startRange() {
        return parseInt(this.range[0], 10);
      },
      endRange() {
        return parseInt(this.range[1], 10);
      },
      itemWidth() {
        let range = this.endRange - this.startRange;
        if (range < 1) {
          return `100%`;
        } else {
          let a = (100 / (range + 1)).toFixed(3);
          let b = (15 * range) / (range + 1);
          return `calc(${a}% - ${b}px)`;
        }
      },
      noDataTextProvince() {
        return this.loading && this.data.length ? "加载中" : "无数据";
      },
      noDataTextCity() {
        return this.loading && this.province.areacode
          ? "加载中"
          : "无数据";
      },
      noDataTextDist() {
        return this.loading && this.city.areacode ? "加载中" : "无数据";
      },
      noDataTextStreet() {
        return this.loading && this.dist.areacode ? "加载中" : "无数据";
      },
      noDataTextTown() {
        return this.loading && this.street.areacode
          ? "加载中"
          : "无数据";
      },
      showProvince() {
        return this.startRange === 0;
      },
      showCity() {
        return this.startRange <= 1 && this.endRange >= 1;
      },
      showDist() {
        return this.startRange <= 2 && this.endRange >= 2;
      },
      showStreet() {
        return this.startRange <= 3 && this.endRange >= 3;
      },
      showTown() {
        return this.startRange <= 4 && this.endRange >= 4;
      },
      provinceList() {
        return this.data;
      },
      cityList() {
        let d = [];
        if (this.startRange === 1) {
          d = this.data;
        } else if (this.province && this.province.areacode) {
          const obj = this.provinceList.find(
            (it) => it.areacode === this.province.areacode
          );
          d = obj && obj.children ? obj.children : [];
        }
        return d;
      },
      distList() {
        let d = [];
        if (this.startRange === 2) {
          d = this.data;
        } else if (this.city && this.city.areacode) {
          const obj = this.cityList.find(
            (it) => it.areacode === this.city.areacode
          );
          d = obj && obj.children ? obj.children : [];
        }
        return d;
      },
      streetList() {
        let d = [];
        if (this.startRange === 3) {
          d = this.data;
        } else if (this.dist && this.dist.areacode) {
          const obj = this.distList.find(
            (it) => it.areacode === this.dist.areacode
          );
          d = obj && obj.children ? obj.children : [];
        }
        return d;
      },
      townList() {
        let d = [];
        if (this.startRange === 4) {
          d = this.data;
        } else if (this.street && this.street.areacode) {
          const obj = this.streetList.find(
            (it) => it.areacode === this.street.areacode
          );
          d = obj && obj.children ? obj.children : [];
        }
        return d;
      },
    },
    methods: {
      // 对value进行处理，设置默认值
      valueInit({value}) {
        let ids = getIds4Value.call(this, value);
        let vIds = getIdsArr4Value.call(this, value);

        for (let i = this.startRange; i <= this.endRange; i++) {
          vIds.push(value.substring(config2[i][0], config2[i][1]) + config2[i][2]);
        }
        return new Promise(resolve => {
          this.$http.get("area/getArea", {
            TREEINFO_GLOBALID: ids.join('.'),
            END_LEVEL: this.endRange + 2,
            START_LEVEL: this.startRange + 2,
          }).done(({success, data}) => {
            if (success) {
              resolve({data, vIds});
              this.reference = value;
            }
          });
        })
      },
      valueInitNext(data, vIds) {
        // 往data中注入value对应的子行政区划
        for (let i = 0; i < this.data.length; i++) {
          let item = this.data[i];
          if (item.areacode === vIds[0]) {
            this.$set(item, 'children', data[0].children);
            this[config1[this.startRange]] = item;
            break;
          }
        }
        // 设置页面显示值
        for (let i = this.startRange + 1; i <= this.endRange; i++) {
          let item = this[config1[i - 1]];
          let areacode = vIds[i - this.startRange];

          if (!_.isArray(item.children)) continue;

          for (let j = 0; j < item.children.length; j++) {
            let item2 = item.children[j];
            if (item2.areacode === areacode) {
              this[config1[i]] = item2;
              break;
            }
          }
        }
      },
      isFixed(it) {
        const [, globalid] = this.getLevelAndGlobalid(this.fixedCode);
        return globalid.includes(it.areacode);
      },
      isDisabled(target) {
        let flag = false;
        if (this.fixedCode) {
          flag =
            target.length === 1 &&
            target.every((it) => {
              return this.isFixed(it);
            });
        }
        return this.disabled ? true : flag;
      },
      getCodeArr(code) {
        const str = String(code);
        const arr = [];
        let reduceNum = 2;
        for (let i = 0; i < str.length; i += reduceNum) {
          if (i === 6) {
            reduceNum = 3;
          }
          arr.push(str.slice(i, i + reduceNum));
        }
        return arr;
      },
      getLevelAndGlobalid(id) {
        const arr = this.getCodeArr(id);
        const [level, globalid] = arr.reduce(
          (a, b) => {
            let [level, globalid] = a;
            if (parseInt(b, 10) > 0) {
              level += 1;
              const g = Array.from(globalid[globalid.length - 1]);
              const index = id.indexOf(b);
              g[index] = b.slice(0, 1);
              g[index + 1] = b.slice(1, 2);
              if (b.length === 3) {
                g[index + 2] = b.slice(2, 3);
              }
              globalid.push(g);
            }
            return [level, globalid];
          },
          [2, [new Array(12).fill(0)]]
        );
        return [level, globalid.map((it) => it.join("")).join("."), arr];
      },
      getNodeById(arr, id) {
        let target = null;
        for (let item of arr) {
          if (item.areacode === id) {
            target = item;
          } else if (item.children && item.children.length) {
            target = this.getNodeById(item.children, id);
          }
          if (target) {
            break;
          }
        }
        return target;
      },
      componentValueChange() {
        let target, text;
        switch (this.endRange) {
          case 4:
            target = this.town;
            text = `${this.province.name}-${this.city.name}-${this.dist.name}-${this.street.name}-${this.town.name}`;
            break;
          case 3:
            target = this.street;
            text = `${this.province.name}-${this.city.name}-${this.dist.name}-${this.street.name}`;
            break;
          case 2:
            target = this.dist;
            text = `${this.province.name}-${this.city.name}-${this.dist.name}`;
            break;
          case 1:
            target = this.city;
            text = `${this.province.name}-${this.city.name}`;
            break;
          case 0:
            target = this.province;
            text = `${this.province.name}`;
            break;
        }
        if (target && target.areacode) {
          //用于解决this.startRange不为0时, text有undefined 的问题
          const textArr = text.split('-');
          const tempArr = textArr.filter(item => item !== "undefined");
          const data = {
            value: target.areacode,
            text: tempArr.join('-'),
          };
          this.reference = data;
          this.emitValueChange(data);
          this.$nextTick(() => {
            this.emitFormValidate(data);
          })
        }
      },
      setValueData(it, type, isUpdate = false) {
        let obj = null;
        switch (type) {
          case 1:
            obj = this.province = Object.assign({}, isUpdate ? this.province : it);
            if (this.autoSelect && obj && !this.city.areacode && obj.children.length) {
              this.onChange(obj.children[0], 2, true);
            }
            break;
          case 2:
            obj = this.city = Object.assign({}, isUpdate ? this.city : it);
            if (this.autoSelect && obj && !this.dist.areacode && obj.children.length) {
              this.onChange(obj.children[0], 3, true);
            }
            break;
          case 3:
            obj = this.dist = Object.assign({}, isUpdate ? this.dist : it);
            if (this.autoSelect && obj && !this.street.areacode && obj.children.length) {
              this.onChange(obj.children[0], 4, true);
            }
            break;
          case 4:
            obj = this.street = Object.assign({}, isUpdate ? this.street : it);
            if (this.autoSelect && obj && !this.town.areacode && obj.children.length) {
              this.onChange(obj.children[0], 5, true);
            }
            break;
          case 5:
            this.town = Object.assign({}, isUpdate ? this.town : it);
            break;
        }
      },
      loadData(node = null, type=0, isAuto = false) {
        // 如果本地存在数据，则不执行请求
        if (this.endRange + 1 == type || (node && node.children && node.children.length)) {
          this.autoSelect && this.setValueData(node, type, false);
          return;
        }
        const range = this.range;
        const startLevel = this.startRange + 2;
        const endLevel = this.endRange + 2;
        const level =
          node && node._level
            ? type + 2
            : startLevel;
        const globalid = node && node.areacode ? node.areacode : this.superCode;
        let ids = ['000000000000'];
        if (node && node._level) {
          for (let i = 0; i < level - 2; i++) {
            ids.push(globalid.substring(config2[i][0], config2[i][1]) + config2[i][2]);
          }
        }

        let params = {
          TREEINFO_GLOBALID: ids.join('.'),
          END_LEVEL: level,
          START_LEVEL: level,
        };
        if (!node && !this.data.length && this.fixedCode) {
          const [level, gid] = this.getLevelAndGlobalid(this.fixedCode);
          params.TREEINFO_GLOBALID = gid.split(".").slice(0, 2).join(".");
          params.END_LEVEL = level;
        }
        return new Promise(resolve => {
          this.getData(params, (data) => {
            const d = this.dataFormat(data);
            if (node) {
              const target = this.getNodeById(this.data, node.areacode);
              if (target) {
                target.loaded = true;
                target.children = d;
                this.setValueData(target, type, !isAuto);
              }
            } else {
              this.data = d;
            }
            this.$nextTick(() => {
              resolve();
            })
          });
        })
      },
      dataFormat(arr) {
        let temp = [...arr];
        temp = temp.map((it) => {
          if (this.fixedCode && this.isFixed(it)) {
            this.setValueData(it);
          }
          if (this.value && this.value.value) {
            if (it.areacode === this.value.value) {
              this.setValueData(it);
            }
          }
          return {
            ...it,
            loaded: !!(it.children && it.children.length),
            children:
              it.children && it.children.length
                ? this.dataFormat(it.children)
                : [],
          };
        });
        return temp;
      },
      getData(params, callback) {
        this.loading = true;
        this.$http
          .get("area/getArea", params)
          .done(({success, data}) => {
            if (success) {
              callback && callback(data);
            }
          })
          .always(() => {
            this.loading = false;
          });
      },
      onChange(v, type, isAuto = false) {
        switch (type) {
          case 1:
            this.city = {};
            this.dist = {};
            this.street = {};
            this.town = {};
            break;
          case 2:
            this.dist = {};
            this.street = {};
            this.town = {};
            break;
          case 3:
            this.street = {};
            this.town = {};
            break;
          case 4:
            this.town = {};
            break;
        }
        if (v && v !== "") {
          this.loadData(v, type, isAuto);
        }
        this.componentValueChange();
      },
      //级联控件的方法
      caGetDefaultList: async function () {
        const self = this;
        const code = self.startRange!==0 && self.fixedCode? self.fixedCode: self.superCode;
        const [level, globId] = self.getLevelAndGlobalid(code);
        const q = {
          id: globId,
          startLevel: level,
          endLevel: level
        };

        self.caOptions = await queryList.call(self, q).catch(err => console.log(err));

        const lazyLoad = {
          lazy: true,
          lazyLoad(node, resolve) {
            doLazyLoad.call(self, node, resolve);
          }
        }

        self.caProps = Object.assign({}, self.caProps, lazyLoad);

      },
      caGetValueList () {
        const self = this;
        const code = self.value.value;
        self.caLoading = true;

        //清空caProps
        self.caProps = {
          lazy: false,
          value: "areacode",
          label: "name",
          emitPath: false
        };

        //如果选择的是 省-村 查询会数据量过大，需要特殊处理
        const arr1 = getIds4Value.call(this, code);
        if (self.startRange === 0 && self.endRange === 4){
          const arr2 = arr1.slice(0, self.startRange + 2);
          const q = {
            id: arr2.join('.'),
            startLevel: self.startRange + 2,
            endLevel: self.endRange + 2
          };
          self.caQueryMultipleData(q);
          return;
        }

        const arr2 = arr1.slice(0, self.startRange + 1);
        const q = {
          id: arr2.join('.'),
          startLevel: self.startRange + 2,
          endLevel: self.endRange + 2
        };

        queryList.call(self, q, "haveData")
          .then(data => {
            self.caLoading = false;
            self.caOptions = data;
            const lazyLoad = {
              lazy: true,
              lazyLoad(node, resolve) {
                doLazyLoad.call(self, node, resolve);
              }
            }
            self.caProps = Object.assign({}, self.caProps, lazyLoad);
            self.$nextTick(() => {
              self.caValue = code;
              self.reference = self.value;
            })

          })
          .catch(err => console.log(err));

      },
      caQueryMultipleData: async function(q) {
        const self = this;
        const code = self.value.value;
        const _defQ = {
          id: "000000000000",
          startLevel: 2,
          endLevel: 2
        }

        const parentArr = await queryList.call(self, _defQ).catch(err => console.log(err));

        queryList.call(self, q, "haveData")
          .then(data => {
            self.caLoading = false;
            self.caOptions = parentArr.map(par => {
              if (par.areacode === data[0]["areacode"]) {
                par = data[0];
              }
              return par
            });
            const lazyLoad = {
              lazy: true,
              lazyLoad(node, resolve) {
                doLazyLoad.call(self, node, resolve);
              }
            }
            self.caProps = Object.assign({}, self.caProps, lazyLoad);
            self.$nextTick(() => {
              self.caValue = code;
              self.reference = self.value;
            })

          })
          .catch(err => console.log(err));
      },
      caChange(item) {
        const nodes= this.$refs["elCascader"].getCheckedNodes();
        const label = nodes[0].pathLabels.join('-');
        const id = _.last(item.split("."));
        const data = {
          text: label,
          value: id
        };
        this.reference = data;
        this.emitValueChange(data);
      },
    },
    design: {
      layout: {
        exclude: ["value", "items"],
      },
    },
  });

  function queryList(q, type="empty") {
    return new Promise((resolve, reject) => {
      const leafIndex = this.endRange + 2;
      const _q = {
        "TREEINFO_GLOBALID": q.id,
        "START_LEVEL": q.startLevel,
        "END_LEVEL": q.endLevel
      };
      this.$http.get("/area/getArea", _q)
        .done(res => {
          if (!res.success) {
            reject(res.message)
            return
          }

          if (type === "empty"){
            const result = res.data.map(item => {
              const arr = getGlobIds4Value.call(this, item.areacode);
              item.leaf = arr.length === leafIndex;
              return item;
            })
            resolve(result);
          }
          else if (type === "haveData") {
            //递归直到children 为空
            const recursion = arr => {
              if (_.isArray(arr) && arr.length > 0){
                for (let item of arr){
                  if (_.isArray(item.children) && item.children.length > 0){
                    recursion(item.children)
                  }
                  else {
                    item.leaf = true
                  }
                }
              }
              return arr
            }
            const result = recursion(res.data);
            resolve(result);
          }
        })
        .error(err => {
          reject(err)
        })
    })
  }

  function doLazyLoad(node, resolve) {
    const leafIndex =  this.endRange - this.startRange;
    const arr = getGlobIds4Value.call(this, node.value);

    let size = arr.length;
    let level = size + 1;
    const q = {
      "TREEINFO_GLOBALID": arr.join('.'),
      "START_LEVEL": level,
      "END_LEVEL": level
    }

    this.$http.get("/area/getArea", q)
      .done(res => {
        if (!res.success) {
          resolve([]);
          return
        }
        const result = res.data.map(item => {
          item.leaf = node.level === leafIndex;
          return item;
        })
        resolve(result);
      })
      .error(err => {
        console.log(err)
        resolve([])
      })
  }

  function getIds4Value(value) {
    let ids = ['000000000000'];
    for (let i = 0; i < this.startRange + 1; i++) {
      ids.push(value.substring(config2[i][0], config2[i][1]) + config2[i][2]);
    }
    return ids
  }

  function getIdsArr4Value(value) {
    const vIds = [];
    for (let i = this.startRange; i <= this.endRange; i++) {
      vIds.push(value.substring(config2[i][0], config2[i][1]) + config2[i][2]);
    }
    return vIds
  }

  function getGlobIds4Value(value) {
    const arr1 = getIds4Value.call(this, value);
    const arr2 = getIdsArr4Value.call(this, value);
    return _.uniq(arr1.concat(arr2))
  }

</script>